import React, { useState, useEffect } from "react";
import { useAuth } from "../../../context/auth";
import { useProfile } from "../../../context/profile";
import { FormControlLabel, Switch, TextField } from "@material-ui/core";
import { Container, Row, Col, Button, Card, CardBody } from "reactstrap";

import GenderSelect from "./Fields/GenderSelect";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faMapMarkedAlt,
  faPhoneAlt,
  faSave,
  faUser,
  faUsers,
  faPeopleArrows,
} from "@fortawesome/free-solid-svg-icons";
import Swal from "sweetalert2";
import API from "../../../utils/Api";

const ProfileDetailsForm = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const selectedProfile = useProfile().selectedProfile;
  const profile_context = useProfile();

  const [isProcessing, setIsProcessing] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true);
  const [theProfile, setTheProfile] = useState("");

  // Personal
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [gender, setGender] = useState("");
  const [age, setAge] = useState("");
  const [bodyTemperature, setBodyTemperature] = useState("");
  // Contact
  const [email, setEmail] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  // Relative
  const [relativeName, setRelativeName] = useState("");
  const [relativeContact, setRelativeContact] = useState("");
  // Address
  const [zipcode, setZipcode] = useState("");
  const [specificAddress, setSpecificAddress] = useState("");
  // Child Companion
  const [childCompanion, setChildCompanion] = useState("");
  // Travel

  useEffect(() => {
    let profile = selectedProfile[1].profile;
    setTheProfile(selectedProfile);

    //Profile
    setFirstName(profile.first_name.toUpperCase());
    setLastName(profile.last_name.toUpperCase());
    setGender(profile.gender);
    setAge(profile.age);
    setBodyTemperature(selectedProfile[1].temperature);

    // Contact
    setEmail(profile.email);
    setMobileNumber(profile.mobile);

    // Relative
    if (profile.relatives.length > 0) {
      setRelativeName(profile.relatives[0].name);
      setRelativeContact(profile.relatives[0].contact);
    }

    // Address
    setSpecificAddress(profile.address.specific_address);
    setZipcode(profile.address.zipcode);

    // Companion
    if (selectedProfile[1].companions.length > 0) {
      if (checkJSON(selectedProfile[1].companions[0].name)) {
        if (selectedProfile[1].companions[0].name !== null) {
          let companions = JSON.parse(selectedProfile[1].companions[0].name);
          companions = JSON.parse(companions[0].name);
          setChildCompanion(companions[0].name);
        } else {
          setChildCompanion("");
        }
      } else {
        let companions = selectedProfile[1].companions[0].name;
        setChildCompanion(companions);
      }
    }
    if (props.setIsPreparingData) {
      props.setIsPreparingData(false);
    }
    return () => {};
  }, [selectedProfile]);

  const handleEdit = () => {
    setIsEdit(!isEdit);
    if (isDisabled) {
      setIsDisabled(null);
    } else {
      setIsDisabled(true);
    }
  };

  const checkJSON = (str) => {
    try {
      JSON.parse(str);
    } catch (e) {
      return false;
    }
    return true;
  };

  const updateProfileHandler = () => {
    props.setIsProcessing(true);
    setIsProcessing(true);
    let token = authToken;

    let profile = theProfile[1];
    let transport_details;
    if (typeof selectedProfile[1].vehicle_details === "object") {
      transport_details = selectedProfile[1].vehicle_details;
    } else {
      transport_details = JSON.parse(selectedProfile[1].vehicle_details);
    }

    let postData = {
      first_name: firstName,
      last_name: lastName,
      gender: gender,
      age: age,
      email: email,
      mobile: mobileNumber,
      zipcode: parseInt(zipcode),
      specific_address: specificAddress,
      temp: bodyTemperature,
      temperature: bodyTemperature,
      barangay: profile.profile.address.barangay,
      person_type: profile.profile.person_type,
      origin: profile.fromLocation,
      destination: profile.toLocation,
      arrival: profile.toDate,
      departure: profile.fromDate,
      transport_type: profile.mode_of_travel_id,
      transport_no: parseInt(transport_details.transport_no),
      ship_name: transport_details.transport_vehicle,
      travel_id: profile.id,
      accommodation_type: profile.accommodation_type,
      previous_travel: profile.previous_place_travel,
      parking_space_no: profile.parking_space_no,
      seat_no: profile.seat_no,
      route: profile.route,
      companion_name: childCompanion,
      relative_name: relativeName,
      relative_contact: relativeContact,
      scheduled_transportation_id: profile.scheduled_transportation_id,
    };

    API.put(`carrier/update/${profile.profile_id}`, postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Updated!",
            "success"
          );

          profile_context.loadPassengers();
          props.toggle();
        } else {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        props.setIsProcessing(false);
        setIsProcessing(false);

        Swal.fire("Transaction Failed!", "error");
      });
  };

  return (
    <Container fluid className="pt-2">
      {!props.isPreparingData && (
        <div>
          <FormControlLabel
            className="ml-2"
            control={
              <Switch checked={isEdit} onChange={handleEdit} color="primary" />
            }
            label="Edit"
          />
          <Col xs={12} className="mt-3">
            <Row>
              <Col xs={4}>
                <h6 className="text-primary">
                  <FontAwesomeIcon icon={faUser} /> Personal Details
                </h6>
                <Row>
                  <Col xs={12}>
                    <Card className="p-1">
                      <CardBody className="pt-1 mt-3">
                        <div>
                          <TextField
                            name="first_name"
                            label="First Name"
                            value={firstName}
                            onChange={(e) => setFirstName(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            name="last_name"
                            label="Last Name"
                            value={lastName}
                            onChange={(e) => setLastName(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            type="number"
                            name="age"
                            label="Age"
                            value={age}
                            onChange={(e) => setAge(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <GenderSelect
                            gender={gender}
                            disabled={isDisabled}
                            setGender={setGender}
                          />
                        </div>
                        <div className="mb-4">
                          <TextField
                            type="number"
                            inputProps={{
                              min: "20.0",
                              max: "50.0",
                              step: "0.1",
                            }}
                            name="bodyTemperature"
                            label="Body Temperature"
                            value={bodyTemperature || ""}
                            onChange={(e) => setBodyTemperature(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </Col>
              <Col xs={4} className="p-0">
                <h6 className="text-primary">
                  <FontAwesomeIcon icon={faPhoneAlt} /> Contact Details
                </h6>
                <Row>
                  <Col xs={12}>
                    <Card className="p-1">
                      <CardBody className="pt-1">
                        <div>
                          <TextField
                            name="mobile"
                            type="number"
                            label="Mobile Number"
                            value={mobileNumber || ""}
                            onChange={(e) => setMobileNumber(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div>
                          <TextField
                            type="email"
                            name="email"
                            label="Email Address"
                            value={email || ""}
                            onChange={(e) => setEmail(e.target.value)}
                            fullWidth
                            disabled={isDisabled}
                          />
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
                <h6 className="text-primary mt-2 ml-1">
                  <FontAwesomeIcon icon={faMapMarkedAlt} /> Address
                </h6>
                <Row>
                  <Col xs={12}>
                    <Card className="p-1">
                      <CardBody className="pt-1">
                        <div className="mt-2">
                          <TextField
                            name="zipcode"
                            label="Zipcode"
                            value={zipcode || ""}
                            onChange={(e) => setZipcode(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div className="mb-2">
                          <TextField
                            label="Complete Address"
                            name="complete_address"
                            value={specificAddress}
                            onChange={(e) => setSpecificAddress(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </Col>
              <Col xs={4}>
                <h6 className="text-primary">
                  <FontAwesomeIcon icon={faUsers} /> Companion Details
                </h6>
                <Row>
                  <Col xs={12}>
                    <Card className="p-1">
                      <CardBody className="pt-1">
                        <div className="mt-2 mb-2">
                          <TextField
                            label="Child Companion"
                            name="childCompanion"
                            value={childCompanion}
                            helperText="Use comma (,) as separator for multiple child"
                            onChange={(e) => setChildCompanion(e.target.value)}
                            fullWidth
                            disabled={isDisabled}
                          />
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
                <h6 className="text-primary mt-3">
                  <FontAwesomeIcon icon={faPeopleArrows} /> Nearest Relative
                </h6>
                <Row>
                  <Col xs={12}>
                    <Card className="p-1">
                      <CardBody className="pt-1">
                        <div className="mt-2">
                          <TextField
                            label="Name"
                            name="relativeName"
                            value={relativeName}
                            onChange={(e) => setRelativeName(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                        <div className="mb-2">
                          <TextField
                            type="number"
                            label="Contact Number"
                            name="relativeContact"
                            value={relativeContact}
                            onChange={(e) => setRelativeContact(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </Col>
            </Row>
            {!isProcessing && (
              <div>
                {isEdit && (
                  <Row className="mt-3 ml-1">
                    <Button
                      type="button"
                      onClick={() => updateProfileHandler()}
                      color="primary"
                      disabled={isDisabled}
                      style={{ minWidth: "120px" }}
                    >
                      <FontAwesomeIcon icon={faSave} /> Save
                    </Button>
                  </Row>
                )}
              </div>
            )}
          </Col>
        </div>
      )}
    </Container>
  );
};

export default ProfileDetailsForm;
