import React, { useState } from "react";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

const ScheduleSelect = (props) => {
  const [selectedSchedule, setSelectedSchedule] = useState("");

  const handleScheduleSelection = (value) => {
    setSelectedSchedule(value);
    if (props.handleScheduleSelection) {
      props.handleScheduleSelection(value);
    }
  };

  return (
    <div>
      <TextField
        select
        label="Select Schedule"
        name="schedule"
        value={props.selectedSchedule || selectedSchedule}
        onChange={(e) => handleScheduleSelection(e.target.value)}
        fullWidth
        required
        disabled={!props.disabled || null}
        className="text-left mb-1"
      >
        {props.scheduleOptions.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </div>
  );
};

export default ScheduleSelect;
