import React, { useState } from 'react'
import { BrowserRouter as Router, Route, Switch } from "react-router-dom"

import PrivateRoute from '../PrivateRoute'
import { AuthContext } from "../context/auth"

import './App.css'

import Login from '../components/Login/Login'
import Register from '../components/Register/Register'
import Dashboard from '../components/Dashboard/Dashboard'
import Error404 from '../components/UI/Error/Error404'


const App = (props) => {
  const existingTokens = JSON.parse(sessionStorage.getItem("transporttokens"))
  const [authTokens, setAuthTokens] = useState(existingTokens)

  const setTokens = (data) => {
    sessionStorage.setItem("transporttokens", JSON.stringify(data))
    setAuthTokens(data)
  }

  return (
    <AuthContext.Provider value={{ authTokens, setAuthTokens: setTokens }}>
      <Router basename="/marina/module/transport">
        <Switch>
          <PrivateRoute exact path="/" component={Dashboard} />
          <PrivateRoute exact path="/sub-accounts" component={Dashboard} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/register" component={Register} />
          <Route component={Error404} />
        </Switch>
      </Router>
    </AuthContext.Provider>
  )
}

export default App 