import React, { useState } from "react";
import { Modal, ModalHeader, ModalBody, Row, Col } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserCog } from "@fortawesome/free-solid-svg-icons";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import ProfileDetailsForm from "../Forms/ProfileDetailsForm";
import { useTheme } from "../../../context/theme";

const PassengerProfile = (props) => {
  const [isProcessing, setIsProcessing] = useState(false);
  const modalMarginXL = useTheme().modalMarginXL;

  const [isPreparingData, setIsPreparingData] = useState(true);

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-xl"
        style={{ marginTop: "6%", marginLeft: modalMarginXL }}
      >
        <ModalHeader style={{ lineHeight: "2" }} toggle={props.toggle}>
          <FontAwesomeIcon icon={faUserCog} /> Passenger Profile
        </ModalHeader>
        <form onSubmit={(e) => e.preventDefault()}>
          <ModalBody className="pt-0">
            <Row>
              <ProfileDetailsForm
                setIsProcessing={setIsProcessing}
                toggle={props.toggle}
                isPreparingData={isPreparingData}
                setIsPreparingData={setIsPreparingData}
              />
              {isPreparingData && (
                <Col className="text-center mt-2">
                  <LinearProgressBarWithLabel title="Preparing Data..." />
                </Col>
              )}
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Updating Passenger Profile" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
        </form>
      </Modal>
    </div>
  );
};

export default PassengerProfile;
