import React, { useState } from "react";
import { useOptions } from "../../../../context/options";

import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

const CompanySelect = (props) => {
    const companyOptions = useOptions().companyOptions;
    const [company, setCompany] = useState("");

    const handleCompanySelection = (value) => {
        setCompany(value);
        if (props.setCompanyId) {
            props.setCompanyId(value);
        }

        if (props.setCompany) {
            let label = companyOptions.filter((v) => {
                return v.value === value;
            });

            props.setCompany(label[0].label);
        }
    };

    return (
        <div>
            <TextField
                select
                label="Select Company"
                name="company_id"
                value={props.companyName || company}
                onChange={(e) => handleCompanySelection(e.target.value)}
                fullWidth
                required
                disabled={props.disabled || false}
                className="text-left"
            >
                {companyOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>
        </div>
    );
};

export default CompanySelect;
