import React, { useState } from "react";
import { useOptions } from "../../../../context/options";

import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

const ShipNameSelect = (props) => {
  const shipOptions = useOptions().shipOptions;
  const [ship, setShip] = useState("");

  const handleShipSelection = (value) => {
    setShip(value);
    if (props.setShipName) {
      props.setShipName(value);
    }

    if (props.getShipDetails) {
      props.getShipDetails(value);
    }

    if (props.getShipSchedules) {
      props.getShipSchedules(value);
    }

    if (props.setShiplabel) {
      let label = shipOptions.filter((v) => {
        return v.value === value;
      });

      props.setShiplabel(label[0].label);
    }

    if (props.handleChangeShip) {
      props.handleChangeShip(value);
    }
  };

  return (
    <div>
      <TextField
        select
        label="Select a Ship"
        name="shipName"
        value={props.shipName || ship}
        onChange={(e) => handleShipSelection(e.target.value)}
        fullWidth
        required
        disabled={props.disabled || false}
        className="text-left"
      >
        {shipOptions.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </div>
  );
};

export default ShipNameSelect;
