import XLSX from "sheetjs-style";
import ExcelTemplate from "../assets/template/Template.xlsx";
import DateFnsUtils from "@date-io/date-fns";

export const generateManifestsExcel = ({
  records = [],
  dates = [],
  reportType = "",
  shipName = "",
  isDaily = false,
  carrierName = "",
  voyageRoute = "",
  voyageNumber = "",
}) => {
  let dateFns = new DateFnsUtils();
  let reportDate;

  if (!isDaily) {
    let from = dateFns.format(new Date(dates[0]), "MMMM d");
    let to = dateFns.format(new Date(dates[1]), "d, yyyy");

    reportDate = `${from}-${to}`;
  } else {
    reportDate = dateFns.format(new Date(dates[0]), "MMMM d, yyyy");
  }

  if (records.length > 0) {
    fetch(ExcelTemplate)
      .then((res) => res.arrayBuffer())
      .then((ab) => {
        const wb = XLSX.read(ab, { type: "array" });
        const ws = wb.Sheets[wb.SheetNames[0]];

        ws["A1"].s = {
          font: {
            sz: 22,
            bold: true,
            color: { rgb: "00000000" },
          },
          alignment: {
            vertical: "center",
            horizontal: "center",
          },
        };

        ws["A2"].s = {
          font: {
            sz: 11,
            color: { rgb: "00000000" },
          },
          alignment: {
            vertical: "center",
            horizontal: "center",
          },
        };

        XLSX.utils.sheet_add_aoa(
          ws,
          [[`Name of Ship: ${shipName.toUpperCase()}`]],
          {
            origin: "A3",
          }
        );

        ws["A3"].s = {
          font: {
            sz: 16,
            bold: true,
            color: { rgb: "00000000" },
          },
        };

        ws["A7"].s = {
          font: {
            sz: 16,
            bold: true,
            color: { rgb: "00000000" },
          },
        };

        XLSX.utils.sheet_add_aoa(
          ws,
          [[`Name of Company/Operator: ${carrierName.toUpperCase()}`]],
          { origin: "A5" }
        );

        ws["A5"].s = {
          font: {
            sz: 16,
            bold: true,
            color: { rgb: "00000000" },
          },
        };

        XLSX.utils.sheet_add_aoa(
          ws,
          [[`Voyage Route/Link: ${voyageRoute.toUpperCase()}`]],
          { origin: "K3" }
        );

        ws["K3"].s = {
          font: {
            sz: 16,
            bold: true,
            color: { rgb: "00000000" },
          },
        };

        XLSX.utils.sheet_add_aoa(
          ws,
          [[`Date/Voyage Number: ${reportDate} / ${voyageNumber}`]],
          { origin: "K5" }
        );

        ws["K5"].s = {
          font: {
            sz: 16,
            bold: true,
            color: { rgb: "00000000" },
          },
        };

        XLSX.utils.sheet_add_aoa(ws, records, { origin: "A12" });

        let cols = [];
        const range = XLSX.utils.decode_range(ws["!ref"]);
        for (let R = range.s.r; R <= range.e.r; ++R) {
          for (let C = range.s.c; C <= range.e.c; ++C) {
            let cell_address = { c: C, r: R };
            if (R === 8) {
              let cell_ref = XLSX.utils.encode_cell(cell_address);
              ws[cell_ref].s = {
                alignment: {
                  vertical: "center",
                  horizontal: "center",
                  wrapText: true,
                },
              };
              cols.push({ wch: 15 });
            }
          }
        }

        ws["C11"].s = {
          alignment: {
            vertical: "center",
            horizontal: "center",
            wrapText: true,
          },
        };

        ws["D11"].s = {
          alignment: {
            vertical: "center",
            horizontal: "center",
            wrapText: true,
          },
        };

        ws["O10"].s = {
          alignment: {
            vertical: "center",
            horizontal: "center",
            wrapText: true,
          },
        };

        ws["Q10"].s = {
          alignment: {
            vertical: "center",
            horizontal: "center",
            wrapText: true,
          },
        };

        cols[4] = {wch: 8}
        cols[5] = {wch: 8}
        cols[7] = {wch: 10}
        ws["!cols"] = cols;

        XLSX.writeFile(
          wb,
          `PEMS ${reportType} Report Generated on ${dateFns.format(
            new Date(),
            "MMM-dd-yyyy"
          )}.xlsx`
        );
      });
  }
};
