import React, { useState } from "react";
import {
  MuiPickersUtilsProvider,
  KeyboardDateTimePicker,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";

const DepartureDate = (props) => {
  const [departureDate, setDepartureDate] = useState(null);

  const handleDepartureDateChange = (dateSelected) => {
    setDepartureDate(dateSelected);

    if (props.setDepartureDateChange) {
      props.setDepartureDateChange(dateSelected);
    }

    if (props.pickedDate) {
      props.pickedDate(dateSelected);
    }
  };

  return (
    <div>
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <KeyboardDateTimePicker
          value={departureDate || props.departureDate}
          onChange={(d,v) => handleDepartureDateChange(d)}
          name="departureDate"
          label="ETD (Est. Time of Departure)"
          format="MMM/d/yyyy h:mm:ss a"
          required
          disablePast
          clearable
          hideTabs
          showTodayButton
          fullWidth
          helperText=""
          error={false}
          disabled={props.disabled}
        />
      </MuiPickersUtilsProvider>
    </div>
  );
};

export default DepartureDate;
