import React, { useState } from "react";
import API from "../../../utils/Api";
import { useAuth } from "../../../context/auth";
import { useShip } from "../../../context/ship";
import { useTheme } from "../../../context/theme";
import { useOptions } from "../../../context/options";
import Swal from "sweetalert2";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Card,
  CardBody,
} from "reactstrap";

import { TextField } from "@material-ui/core";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faInfoCircle,
  faSave,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons";
import TransportType from "../../UI/Forms/Fields/TransportType";
import DirectionsBoatSharp from "@material-ui/icons/DirectionsBoatSharp";

const NewShip = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const ship_context = useShip();
  const options_context = useOptions();
  const modalMarginLG = useTheme().modalMarginLG;
  const [isProcessing, setIsProcessing] = useState(false);
  const [shipName, setShipName] = useState(null);
  const [capacity, setCapacity] = useState(0);
  const [voyageRoute, setVoyageRoute] = useState(null);
  const [transportType, setTransportType] = useState(null);

  const saveNewRecordHandler = (e) => {
    e.preventDefault();
    setIsProcessing(true);
    let token = authToken;
    let postData = {
      name: shipName,
      capacity: capacity,
      routes: voyageRoute,
      transportation_category_id: transportType,
    };

    API.post("store/ships/store", postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false);

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Created!",
            "success"
          );

          ship_context.loadShips();
          options_context.getShipsOptions();
          props.toggle();
        } else {
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        setIsProcessing(false);
        Swal.fire("Transaction Failed!", "error");
      });
  };

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-lg"
        style={{ marginTop: "5%", marginLeft: modalMarginLG }}
      >
        <ModalHeader toggle={props.toggle}>
          <DirectionsBoatSharp /> New Ship
        </ModalHeader>
        <form onSubmit={(e) => saveNewRecordHandler(e)}>
          <ModalBody>
            <Row className="p-2">
              <Col md={12}>
                <Card>
                  <CardBody>
                    <Row>
                      <Col md={12} className="mt-0 pt-0 mb-2 text-danger">
                        <h6>
                          <FontAwesomeIcon icon={faInfoCircle} /> Note: Each
                          ship name should be unique and please fill in the required (*) fields
                        </h6>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={6}>
                        <TextField
                          className="m-0"
                          onChange={(e) => setShipName(e.target.value)}
                          name="shipName"
                          label="Ship Name"
                          required
                          fullWidth
                        />
                        <TextField
                          type="number"
                          InputProps={{ inputProps: { min: 0 } }}
                          className="m-0"
                          onChange={(e) => setCapacity(e.target.value)}
                          name="capacity"
                          label="Capacity"
                          required
                          fullWidth
                        />
                      </Col>
                      <Col md={6}>
                        <TextField
                          className="m-0"
                          onChange={(e) => setVoyageRoute(e.target.value)}
                          name="voyageRoute"
                          label="Route"
                          required
                          fullWidth
                        />
                        <TransportType setTransportType={setTransportType} />
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Creating New Ship Record" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faSave} /> Save
            </Button>{" "}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default NewShip;
