import React, { useState, useEffect, useCallback } from "react";
import DateFnsUtils from "@date-io/date-fns";
import Swal from "sweetalert2";
import { useAuth } from "../../../context/auth";
import API from "../../../utils/Api";
import { ScheduleContext } from "../../../context/schedules";
import { ShipContext } from "../../../context/ship";
import { debounce, isArray } from "lodash";
import { Tooltip, FormControlLabel, Switch } from "@material-ui/core";
import { Container, Button, Row, Col } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArchive, faEdit } from "@fortawesome/free-solid-svg-icons";
import DataTable from "../../UI/Datatable/Datatable";
import MenuButton from "../../UI/Common/OptionMenuSchedulesButton";
import NewSchedules from "../../UI/Modals/NewSchedule";
import UpdateSchedule from "../../UI/Modals/UpdateSchedule";
import Axios from "axios";

const Schedules = (props) => {
  const dateFns = new DateFnsUtils();
  const token = useAuth().authTokens;
  const [scheduleData, setScheduleData] = useState([]);
  const [selectedSchedule, setSelectedSchedule] = useState("");
  const [currentPage, setCurrentPage] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [shipOptions, setShipOptions] = useState([]);

  // Modals
  const [newScheduleModal, setNewScheduleModal] = useState(false);
  const toggleNewScheduleModal = () => setNewScheduleModal(!newScheduleModal);
  const [updateScheduleModal, setUpdateScheduleModal] = useState(false);
  const toggleUpdateScheduleModal = () =>
    setUpdateScheduleModal(!updateScheduleModal);

  const statusButton = (index, schedule, data) => {
    return (
      <FormControlLabel
        className="ml-2"
        control={
          <Switch
            checked={schedule.isActive ? true : false}
            onChange={(e) => handleUpdateStatus(index, schedule, data)}
            color={schedule.isActive ? "primary" : "default"}
          />
        }
        label={schedule.isActive ? "Active" : "Inactive"}
      />
    );
  };

  const formatArrayData = (data) => {
    if (data.length > 0) {
      let formatted = [];

      return data.map((value, index) => {
        let schedule = value;
        let ship = value.ship;

        formatted = [
          schedule.id,
          schedule.voyage,
          ship.name,
          schedule.fromLocation,
          schedule.toLocation,
          dateFns.format(new Date(schedule.departure), "MMM/d/yyyy h:mm:ss a"),
          dateFns.format(new Date(schedule.arrival), "MMM/d/yyyy h:mm:ss a"),
          statusButton(index, value, data),
          [index, value],
        ];

        return formatted;
      });
    }
  };

  const formatData = (data = null) => {
    if (data === null) {
      return null;
    }

    if (isArray(data)) {
      return formatArrayData(data);
    }

    return null;
  };

  const loadSchedules = useCallback(() => {
    setIsLoading(true);

    let cancelToken = Axios.cancelToken;

    API.get("carrier/transport/voyage/ships", {
      cancelToken: cancelToken,
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    })
      .then((result) => {
        let data = result.data.data;
        let currentPage = result.data.current_page;
        let schedules = formatData(data);     
        setCurrentPage(currentPage);
        setScheduleData(schedules);
        setIsLoading(false);
      })
      .catch((e) => {
        setIsLoading(false);
      });
  }, []);

  const loadShips = useCallback(() => {
    API.get("carrier/transport/ships", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let ships = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempShipArr = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          ships.push(tempShipArr);
        }
      }

      setShipOptions(ships);
    });
  }, []);

  const appendSchedules = (page) => {
    API.get(`carrier/transport/voyage?page=${page}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let data = result.data.data;

      let newSchedulesToAppend = [];
      let schedules = formatData(data);

      if (result.data.current_page > currentPage) {
        setCurrentPage(result.data.current_page);
        if (schedules !== undefined) {
          newSchedulesToAppend = [...scheduleData, ...schedules];
        } else {
          newSchedulesToAppend = scheduleData;
        }
      }

      setScheduleData(newSchedulesToAppend);
    });
  };

  const debounceSearchHandler = debounce(
    (queryData) => searchSchedules(queryData),
    500
  );

  const searchSchedules = (query) => {
    return API.post(
      `carrier/transport/voyage/search`,
      {
        name: query,
      },
      {
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    ).then((result) => {
      let data = result.data;
      let schedules = formatData(data);
      setScheduleData(schedules);
      setIsLoading(false);
    });
  };

  const archiveHandler = (data) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        API.delete(`carrier/transport/voyage/delete/${data[1].id}`, {
          headers: {
            Authorization: "bearer " + token.access_token,
          },
        }).then((result) => {
          if (result.data.status) {
            Swal.fire("Transaction Completed", result.data.message, "success");
            loadSchedules();
          } else {
            Swal.fire("Transaction Completed", result.data.message, "error");
          }
        });
      }
    });
  };

  const handleScheduleDetails = (data) => {
    setSelectedSchedule(data);
    toggleUpdateScheduleModal();
  };

  const handleUpdateStatus = (index, schedule, data) => {
    let raw = data;
    let message = "Activate this schedule?";
    let icon = "info";
    let title = "Activating"
    
    if (schedule.isActive) {
      message = "Deactivate this schedule?";
      icon = "warning";
      title = "Deactivating";
    }

    Swal.fire({
      title: `${title} Schedule`,
      text: message,
      icon: icon,
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        API.put(
          `carrier/transport/voyage/status/${schedule.id}`,
          { isActive: !schedule.isActive },
          {
            headers: {
              Authorization: "bearer " + token.access_token,
            },
          }
        ).then((result) => {
          if (result.data.status) {
            raw[index].isActive = !schedule.isActive;
            let formattedData = formatData(raw);
            setScheduleData(formattedData);
          } else {
            Swal.fire("Transaction Completed", result.data.message, "error");
          }
        });
      }
    });
  };

  const columns = [
    "ID",
    "Voyage No.",
    "Ship Name",
    "Origin",
    "Destination",
    "Estimated Time of Departure",
    "Estimated Time of Arrival",
    "Status",
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div style={{ color: "#1a1751" }}>
              <Tooltip title="Edit Schedule" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn"
                  onClick={() => handleScheduleDetails(value)}
                >
                  <FontAwesomeIcon icon={faEdit} />
                </Button>
              </Tooltip>
              <Tooltip title="Archive Schedule" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn text-danger"
                  onClick={() => archiveHandler(value)}
                >
                  <FontAwesomeIcon icon={faArchive} />
                </Button>
              </Tooltip>
            </div>
          );
        },
      },
    },
  ];

  const options = {
    fixHeader: true,
    filterType: "dropdown",
    responsive: "vertical",
    tableBodyHeight: "350px",
    selectableRows: "none",
    filter: false,
    viewColumns: false,
    elevation: 0,
    print: false,
    download: false,
    rowsPerPage: 5,
    rowsPerPageOptions: [5, 30, 100],
    onChangePage: (p) => {
      if (p % 2 !== 0) {
        if (p >= currentPage) {
          appendSchedules(currentPage + 1);
        }
      }
    },
    onSearchClose: () => {
      setIsLoading(false);
      loadSchedules();
    },
    searchProps: {
      onKeyUp: (e) => {
        let queryString = e.target.value;
        if (queryString.length >= 2) {
          setIsLoading(true);
          debounceSearchHandler(queryString);
        }
      },
    },
    // this is to disable auto search on local data
    customSearch: (query, row, col) => {
      return scheduleData.length > 0;
    },
  };

  useEffect(() => {
    loadSchedules();
    loadShips();
    return () => {};
  }, [loadSchedules, loadShips]);

  return (
    <ScheduleContext.Provider value={{ loadSchedules, selectedSchedule }}>
      <ShipContext.Provider value={{ shipOptions }}>
        <Container fluid="true" className="p-0 m-0 ">
          <Row className="m-0 p-0 text-right">
            <Col md={12}>
              <Row>
                <Col md={12} className="m-0 p-0">
                  <MenuButton toggle={toggleNewScheduleModal} />
                </Col>
              </Row>
            </Col>
            <Col md={12} className="pl-0 ml-0 pr-0 mr-0 text-left">
              <DataTable
                title="Voyage Schedules"
                loadSchedules={loadSchedules}
                data={scheduleData}
                isLoading={isLoading}
                columns={columns}
                options={options}
              />
            </Col>
          </Row>
        </Container>
        {newScheduleModal && (
          <NewSchedules
            isOpen={newScheduleModal}
            toggle={toggleNewScheduleModal}
          />
        )}
        {updateScheduleModal && (
          <UpdateSchedule
            isOpen={updateScheduleModal}
            toggle={toggleUpdateScheduleModal}
          />
        )}
      </ShipContext.Provider>
    </ScheduleContext.Provider>
  );
};

export default Schedules;
