import React, { useState } from 'react';

import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';

const AccommodationType = (props) => {

    const [accommodationType, setAccommodationType] = useState('');

    const accommodationTypeOptions = [
        {
            value: "1st",
            label: "1st Class"
        },
        {
            value: "2nd",
            label: "2nd Class"
        },
        {
            value: "3rd",
            label: "3rd Class"
        }
    ]

    const handleChange = (value) => {
        setAccommodationType(value)
        if(props.setAccommodationType){
            props.setAccommodationType(value)
        }        
    }

    return (
        <div>
            <TextField
                select
                label="Select Accommodation type"
                name="accommodationType"
                value={props.accommodationType || accommodationType}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                disabled={props.disabled}
            >
                {accommodationTypeOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>
        </div>
    )
}

export default AccommodationType;