import React, { useState } from "react";
import API from "../../../utils/Api";
import DateFnsUtils from "@date-io/date-fns";
import { useAuth } from "../../../context/auth";
import { useSchedule } from "../../../context/schedules";
import { useTheme } from "../../../context/theme";
import Swal from "sweetalert2";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Card,
  CardBody,
} from "reactstrap";

import { TextField } from "@material-ui/core";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCalendarDay,
  faInfoCircle,
  faSave,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons";
import Error from "../Error/Error";
import ShipNameSelect from "../Forms/Fields/ShipNameSelect";
import ArrivalDate from "../Forms/Fields/ArrivalDate";
import DepartureDate from "../Forms/Fields/DepartureDate";
import { Typography } from "@material-ui/core";

const NewSchedule = (props) => {
  const dateFns = new DateFnsUtils();
  const authToken = useAuth().authTokens.access_token;
  const schedule_context = useSchedule();
  const modalMarginLG = useTheme().modalMarginLG;

  const [isProcessing, setIsProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("Invalid Input");
  const [isSelectedDeparture, setIsSelectedDeparture] = useState(false);

  const [ship, setShip] = useState(null);
  const [voyage, setVoyage] = useState(0);
  const [departure, setDeparture] = useState(null);
  const [arrival, setArrival] = useState(null);
  const [fromLocation, setFromLocation] = useState("");
  const [toLocation, setToLocation] = useState("");

  const handleDepartureDateChange = () => {
    setIsSelectedDeparture(true);
  };

  const validate = () => {
    let d1 = dateFns.date(departure);
    let d2 = dateFns.date(arrival);
    let today = dateFns.date(Date.now());
    
    if (dateFns.isAfter(d1, today)) {
      setIsError(true);
      setErrorMessage("Invalid Departure Date");
      return false;
    }

    if (dateFns.isBefore(d2, d1) || dateFns.isEqual(d1, d2)) {
      setIsError(true);
      setErrorMessage("Invalid Arrival Date");
      return false;
    }
    return true;
  };

  const saveNewRecordHandler = (e) => {
    e.preventDefault();

    if (!validate()) {
      return null;
    }

    setIsProcessing(true);
    let token = authToken;
    let postData = {
      transportation_id: ship,
      voyage: voyage,
      departure: dateFns.format(new Date(departure), "Y-M-dd H:m:s"),
      arrival: dateFns.format(new Date(arrival), "Y-M-dd H:m:s"),
      fromLocation: fromLocation,
      toLocation: toLocation,
    };

    API.post("carrier/transport/voyage/store", postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false);

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Created!",
            "success"
          );

          schedule_context.loadSchedules();
          props.toggle();
        } else {
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        setIsProcessing(false);
        Swal.fire("Transaction Failed!", "error");
      });
  };

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        className="modal-lg"
        style={{ marginTop: "5%", marginLeft: modalMarginLG }}
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faCalendarDay} /> New Schedule
        </ModalHeader>
        <form onSubmit={(e) => saveNewRecordHandler(e)}>
          <ModalBody>
            {isError && (
              <Col md={12} className="text-left m-0 p-0 ml-1">
                <Typography variant="h6">
                  <Error className="p-0 m-0">{errorMessage}</Error>
                </Typography>
              </Col>
            )}
            <Row className="p-2">
              <Col md={12}>
                <Card>
                  <CardBody>
                    <Row>
                      <Col md={12} className="mt-0 pt-0 mb-2 text-danger">
                        <h6>
                          <FontAwesomeIcon icon={faInfoCircle} /> Note: Please
                          fill in the required (*) fields
                        </h6>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={6}>
                        <ShipNameSelect setShipName={setShip} />
                        <TextField
                          className="m-0"
                          onChange={(e) => setFromLocation(e.target.value)}
                          name="fromLocation"
                          label="Origin"
                          required
                          fullWidth
                        />
                        <TextField
                          className="m-0"
                          onChange={(e) => setToLocation(e.target.value)}
                          name="toLocation"
                          label="Destination"
                          required
                          fullWidth
                        />
                      </Col>
                      <Col md={6}>
                        <TextField
                          className="m-0"
                          onChange={(e) => setVoyage(e.target.value)}
                          name="voyage"
                          label="Voyage Number"
                          required
                          fullWidth
                        />
                        <DepartureDate
                          pickedDate={handleDepartureDateChange}
                          departureDate={departure}
                          setDepartureDateChange={setDeparture}
                        />
                        <ArrivalDate
                          isSelectedDeparture={isSelectedDeparture}
                          departure={departure}
                          arrivalDate={arrival}
                          setArrivalDateChange={setArrival}
                        />
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Creating New Voyage Schedule" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faSave} /> Save
            </Button>{" "}
            <Button
              type="button"
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default NewSchedule;
