import React, { useEffect, useState } from "react";
import API from "../../../utils/Api";
import DateFnsUtils from "@date-io/date-fns";
import { useProfile } from "../../../context/profile";

import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
} from "reactstrap";

import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import CheckBox from "@material-ui/core/Checkbox";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faDownload,
  faFileExcel,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons";
import { useAuth } from "../../../context/auth";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import Error from "../Error/Error";
import ShipNameSelect from "../Forms/Fields/ShipNameSelect";
import { FormControlLabel, Paper, TextField } from "@material-ui/core";
import { generateManifestsExcel } from "../../../utils/excel_manifest";
import { Typography } from "@material-ui/core";

const Report = (props) => {
  const token = useAuth().authTokens;
  const dateFns = new DateFnsUtils();
  const profile_context = useProfile();

  const [isProcessing, setIsProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("No Records Found");

  // checkboxes
  const [isDaily, setIsDaily] = useState(false);
  const [isMonthly, setIsMonthly] = useState(false);
  const [isCustomRange, setIsCustomRange] = useState(false);

  // value
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [daily, setDaily] = useState(null);
  const [monthly, setMonthly] = useState(null);
  const [isStartDateSelected, setIsStartDateSelected] = useState(false);
  const [reportType, setReportType] = useState("Daily");
  const [isDownload, setIsDownload] = useState(false);
  const [shipName, setShipName] = useState("");
  const [voyageNumber, setVoyageNumber] = useState("");

  // for reports
  const [shipLabel, setShiplabel] = useState("");
  const [carrierName, setCarrierName] = useState("");
  const [voyageRoute, setVoyageRoute] = useState("");

  const toggleReportType = (value, type) => {
    const reportTypeOptions = {
      daily: 1,
      monthly: 2,
      custom: 3,
    };

    setIsError(false);
    setIsDownload(value);
    setIsDaily(false);
    setIsMonthly(false);
    setIsCustomRange(false);

    switch (type) {
      case reportTypeOptions.daily:
        setReportType("Daily");
        setIsDaily(!isDaily);
        break;
      case reportTypeOptions.monthly:
        setReportType("Monthly");
        setIsMonthly(!isMonthly);
        break;
      default:
        setReportType("Custom");
        setIsCustomRange(!isCustomRange);
        break;
    }
  };

  const setStartDateChangeHandler = (d) => {
    setStartDate(dateFns.format(new Date(d), "Y-M-dd"));
    if (d !== "") {
      setIsStartDateSelected(true);
    } else {
      setIsStartDateSelected(false);
    }
  };

  const setEndDateChangeHandler = (d) => {
    setEndDate(dateFns.format(new Date(d), "Y-M-dd"));
  };

  const generateReportHandler = (e) => {
    let start = "";
    let end = "";

    e.preventDefault();
    setIsError(false);
    setIsProcessing(true);
    try {
      if (isDaily) {
        // Format date for query
        start = dateFns.format(new Date(daily), "Y-M-dd 00:00:00");
        end = dateFns.format(new Date(daily), "Y-M-dd 23:59:59");
      }

      if (isMonthly) {
        // Format date for query
        start = dateFns.format(
          dateFns.startOfMonth(new Date(monthly)),
          "Y-M-dd 00:00:00"
        );
        end = dateFns.format(
          dateFns.endOfMonth(new Date(monthly)),
          "Y-M-dd 23:59:59"
        );
      }

      if (isCustomRange) {
        start = startDate;
        end = endDate;
      }

      getRecords(start, end)
        .then((result) => {
          if (Object.keys(result.data).length > 0) {
            try {
              let parsedRecords = parseData(result);
              let params = {
                records: parsedRecords,
                dates: [start, end],
                reportType: reportType,
                shipName: shipLabel,
                isDaily: isDaily,
                carrierName: carrierName,
                voyageRoute: voyageRoute,
                voyageNumber: voyageNumber,
              };

              generateManifestsExcel(params);
            } catch (e) {
              setIsError(true);
              setErrorMessage("Template not found");
            }

            setIsProcessing(false);
          } else {
            setIsProcessing(false);
            setErrorMessage("No Records Found");
            setIsError(true);
          }
        })
        .catch((e) => {
          setErrorMessage("Unable to access records");
          setIsError(true);
          setIsProcessing(false);
        });
    } catch (e) {
      setErrorMessage("Code Error");
      setIsError(true);
      setIsProcessing(false);
    }
  };

  const getRecords = (start, end) => {
    return API.post(
      "carrier/download",
      { dates: [start, end], transport: shipName, voyage: voyageNumber },
      {
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    );
  };

  const parseData = (result) => {
    let data = result.data;
    let row;
    let records = [];
    let child = "";
    let relatives = "",
      relativeContact = "";

    for (let key in data) {
      let profile = data[key].profile;
      let companions = data[key].companions;

      if (companions.length > 0) {
        child = "";
        for (let c in companions) {
          if (companions.length === 1) {
            child = companions[c].name;
          } else {
            child = child + ", " + companions[c].name;
          }
        }
      }

      if (profile.relatives.length > 0) {
        relatives = "";
        relativeContact = "";
        for (let key in profile.relatives) {
          if (profile.relatives.length === 1) {
            relatives = profile.relatives[key].name;
            relativeContact = profile.relatives[key].contact;
          } else {
            relatives = relatives + " " + profile.relatives[key].name;
            relativeContact =
              relativeContact + ", " + profile.relatives[key].contact;
          }
        }
      }

      row = [
        profile.first_name.toUpperCase(),
        profile.last_name.toUpperCase(),
        child,
        data[key].accommodation_type,
        profile.gender.toUpperCase(),
        profile.age,
        profile.address.specific_address,
        profile.address.zipcode,
        data[key].fromLocation,
        data[key].toLocation,
        profile.mobile != null ? profile.mobile : "",
        profile.email != null ? profile.email : "",
        relatives,
        relativeContact,
        `${data[key].temperature}` || "",
        data[key].seat_no,
        data[key].parking_space_no || "",
        data[key].previous_place_travel || "",
      ];

      records = [...records, row];
    }

    return records;
  };

  const getShipDetails = (shipId) => {
    API.get(`carrier/transport/ship/${shipId}`, {
      headers: {
        Authorization: "bearer " + token,
      },
    }).then((result) => {
      let data = result.data;
      if (data != null) {
        setVoyageRoute(data.routes);
      }
    });
  };

  useEffect(() => {
    let carrier = profile_context.profileDetails;
    setCarrierName(carrier.carrier_name);
    return () => {};
  }, []);

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={false || props.isOpen}
        toggle={props.toggle}
        className="modal-md mt-5 pt-5"
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faFileExcel} className="text-success mr-1" />
          Passenger Manifest Report
        </ModalHeader>
        <form onSubmit={(e) => generateReportHandler(e)}>
          <ModalBody>
            {isError && (
              <Col md={12} className="text-left m-0 p-0 ml-3">
                <Typography variant="h6">
                  <Error className="p-0 m-0">{errorMessage}</Error>
                </Typography>
              </Col>
            )}
            <Row className="p-2">
              <Col md={12}>
                <Row>
                  <Col md={12}>
                    <Row>
                      <Col md={12}>
                        <Paper className="p-2 pl-4" elevation={0}>
                          <Row>
                            <Col md={{ size: "auto" }} className="m-0 p-0">
                              <FormControlLabel
                                className="mt-1 ml-1 p-0"
                                control={
                                  <CheckBox
                                    className="m-0 p-0 pr-1"
                                    checked={isDaily}
                                    onChange={(e) =>
                                      toggleReportType(e.target.checked, 1)
                                    }
                                    color="primary"
                                    size="small"
                                  />
                                }
                                label="Daily"
                              />
                            </Col>
                            <Col
                              md={{ size: "auto" }}
                              className="m-0 p-0 text-left"
                            >
                              <FormControlLabel
                                className="mt-1 ml-1 p-0"
                                control={
                                  <CheckBox
                                    className="m-0 p-0 pr-1"
                                    checked={isMonthly}
                                    onChange={(e) =>
                                      toggleReportType(e.target.checked, 2)
                                    }
                                    color="primary"
                                    size="small"
                                  />
                                }
                                label="Monthly"
                              />
                            </Col>
                            <Col md={{ size: "auto" }} className="m-0 p-0">
                              <FormControlLabel
                                className="mt-1 ml-1 p-0"
                                control={
                                  <CheckBox
                                    className="m-0 p-0 pr-1"
                                    checked={isCustomRange}
                                    onChange={(e) =>
                                      toggleReportType(e.target.checked, 3)
                                    }
                                    color="primary"
                                    size="small"
                                  />
                                }
                                label="Custom"
                              />
                            </Col>
                          </Row>
                        </Paper>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row className="mt-1">
                  <Col md={12} className="text-center pt-0">
                    <Col md={12} className="text-left p-2">
                      <ShipNameSelect
                        setShipName={setShipName}
                        setShiplabel={setShiplabel}
                        getShipDetails={getShipDetails}
                      />
                      <TextField
                        value={voyageNumber}
                        onChange={(e) => setVoyageNumber(e.target.value)}
                        label="Voyage Number"
                        fullWidth
                        required
                      />
                    </Col>
                    {isDaily && (
                      <Col md={12} className="p-2 pt-0 mt-0">
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                          <KeyboardDatePicker
                            name="daily"
                            value={daily}
                            onChange={(d, v) => setDaily(d)}
                            label="Select Date"
                            format="MMM/d/yyyy"
                            required
                            clearable
                            hideTabs
                            showTodayButton
                            fullWidth
                          />
                        </MuiPickersUtilsProvider>
                      </Col>
                    )}
                    {isMonthly && (
                      <Col md={12} className="p-2 pt-0 mt-0">
                        <MuiPickersUtilsProvider
                          className="m-0 p-0"
                          utils={DateFnsUtils}
                        >
                          <KeyboardDatePicker
                            name="monthly"
                            className="m-0 p-0"
                            variant="dialog"
                            value={monthly}
                            onChange={(d, v) => setMonthly(d)}
                            label="Select Month"
                            format="MMMM yyyy"
                            views={["year", "month"]}
                            openTo="month"
                            required
                            clearable
                            hideTabs
                            fullWidth
                          />
                        </MuiPickersUtilsProvider>
                      </Col>
                    )}
                    {isCustomRange && (
                      <div>
                        <Col md={12} className="p-2 pt-0 mt-0">
                          <MuiPickersUtilsProvider
                            className="float-left"
                            utils={DateFnsUtils}
                          >
                            <KeyboardDatePicker
                              name="startDate"
                              value={startDate}
                              onChange={(d, v) => setStartDateChangeHandler(d)}
                              label="Start Date"
                              format="MMM/d/yyyy"
                              required
                              clearable
                              hideTabs
                              showTodayButton
                              fullWidth
                            />
                          </MuiPickersUtilsProvider>
                          <MuiPickersUtilsProvider
                            className="float-left mt-2"
                            utils={DateFnsUtils}
                          >
                            <KeyboardDatePicker
                              name="endDate"
                              value={endDate}
                              onChange={(d, v) => setEndDateChangeHandler(d)}
                              label="End Date"
                              minDate={dateFns.addDays(new Date(startDate), 1)}
                              format="MMM/d/yyyy"
                              disabled={!isStartDateSelected}
                              required
                              clearable
                              hideTabs
                              fullWidth
                            />
                          </MuiPickersUtilsProvider>
                        </Col>
                      </div>
                    )}
                  </Col>
                </Row>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Generating Report" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            {isDownload && (
              <Button
                type="submit"
                color="success"
                disabled={isProcessing}
                style={{ minWidth: "120px" }}
              >
                <FontAwesomeIcon icon={faDownload} /> Download
              </Button>
            )}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default Report;
