import React, { useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCog,
  faFileImport,
  faUserPlus,
} from "@fortawesome/free-solid-svg-icons";

const StyledMenu = withStyles({
  paper: {
    // border: "1px solid #d3d4d5",
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "center",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "center",
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles((theme) => ({
  root: {
    root: {
      "&:focus": {
        "& .MuiListItemIcon-root":{
          color: "#757575",
        },
        "& .MuiListItemText-primary": {
          color: "#454545",
        },
      },
    },
  },
}))(MenuItem);

export default function OptionMenuButton(props) {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenu = (menu) => {
    const menuOption = {
      manifest: 1,
      passenger: 2
    };

    handleClose();

    switch (menu) {
      case menuOption.manifest:
        if (props.toggleNewManifestModal) {
          props.toggleNewManifestModal();
        }
        break;
      case menuOption.passenger:
        if (props.toggleNewPassengerModal) {
          props.toggleNewPassengerModal();
        }
        break;
      default:
        break;
    }
  };

  return (
    <div className="mt-1 pt-1 ml-1 float-right">
      <Button
        aria-controls="customized-menu"
        aria-haspopup="true"
        variant="contained"
        color="primary"
        onClick={handleClick}
        className="p-1 pr-2 pl-2 menuButton"
      >
        <FontAwesomeIcon icon={faCog} className="mr-1" /> Options
      </Button>
      <StyledMenu
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <StyledMenuItem onClick={() => handleMenu(1)}>
          <ListItemIcon>
            <FontAwesomeIcon icon={faFileImport} />
          </ListItemIcon>
          <ListItemText primary="Import Manifest" />
        </StyledMenuItem>
        <StyledMenuItem onClick={() => handleMenu(2)}>
          <ListItemIcon>
            <FontAwesomeIcon icon={faUserPlus} />
          </ListItemIcon>
          <ListItemText primary="New Passenger" />
        </StyledMenuItem>
      </StyledMenu>
    </div>
  );
}
