import React, { useState } from 'react'
import API from '../../../utils/Api'
import Swal from 'sweetalert2'
import { useAuth } from "../../../context/auth"

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Col
} from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWindowClose, faSave, faKey, faTimesCircle } from '@fortawesome/free-solid-svg-icons'

import { TextField, InputAdornment, IconButton, Paper } from '@material-ui/core'
import Visibility from '@material-ui/icons/Visibility'
import VisibilityOff from '@material-ui/icons/VisibilityOff'
import LinearProgressBarWithLabel from '../Common/LinearProgressBarWithLabel'

const ChangePasswordModal = (props) => {
    const authToken = useAuth().authTokens

    const [isProcessing, setIsProcessing] = useState(false)
    const [isShowNewPass, setIsShowNewPass] = useState(false)
    const [isShowConfirmPass, setIsShowConfirmPass] = useState(false)
    const [isShowOldPass, setIsShowOldPass] = useState(false)
    const [isConfirmError, setIsConfirmError] = useState(false)
    const [isValidateError, setIsValidateError] = useState(false)
    const [isValidateMessage, setIsValidateMessage] = useState("Invalid Input")
    const [isMinimum, setIsMinimum] = useState(false)
    const [newPassword, setNewPassword] = useState('')
    const [oldPassword, setOldPassword] = useState('')

    const toggleShowNewPassword = () => setIsShowNewPass(!isShowNewPass)
    const toggleShowConfirmPassword = () => setIsShowConfirmPass(!isShowConfirmPass)
    const toggleShowOldPassword = () => setIsShowOldPass(!isShowOldPass)

    const changePasswordHandler = (e) => {
        e.preventDefault()
        const token = authToken.access_token

        if(isMinimum){
            setIsValidateMessage("Minimum password length is 6")
        }

        if(isConfirmError){
            setIsValidateMessage("Password doesn't match")
        }

        if (!isConfirmError && !isMinimum) {
            setIsValidateError(false)
            setIsProcessing(true)          

            let newPassData = {
                "password": oldPassword,
                "new_password": newPassword
            }


            API.post('auth/carrier/update-password', newPassData, {
                headers: {
                    'Authorization': 'bearer ' + token,
                }
            }).then(result => {
                setIsProcessing(false)
                if (result.data.status) {
                    Swal.fire(
                        'Transaction Completed!',
                        'Password Successfully Updated!',
                        'success'
                    )

                    props.toggle()
                } else {
                    setIsValidateMessage(result.data.message)
                    Swal.fire(
                        'Transaction Failed!',
                        result.data.message,
                        'error'
                    )                    
                }
            }).catch(e => {
                setIsProcessing(false)
                Swal.fire(
                    'Transaction Failed!',
                    'Failed to Update Password!',
                    'error'
                )
            })
        }else{
            setIsValidateError(true)
        }
    }

    const checkConfirmPassword = (value) => { 
        setIsValidateError(false)
        if (value === newPassword) {            
            setIsConfirmError(false)
        } else {
            setIsConfirmError(true)
        }
    }

    const handleChangePassword = (value) => {           
        setNewPassword(value)
        setIsValidateError(false)
        if (value.replace(/\s+/g, '').length >= 6) {
            setIsMinimum(false)
            if (value === newPassword) {            
                setIsConfirmError(false)
            } else {
                setIsConfirmError(true)
            }   
        } else {  
            setIsMinimum(true)
        }   
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-md" style={{ marginLeft: "40%", marginTop: "7%", color: "#1a1751" }}>
                <ModalHeader toggle={props.toggle}>
                    <FontAwesomeIcon icon={faKey} /> Change Password <br />
                </ModalHeader>
                <form onSubmit={(e) => changePasswordHandler(e)}>                    
                    <ModalBody>
                        {isValidateError &&
                            <div className="text-left card p-1 bg-danger">
                                <span className="text-white ml-2"><FontAwesomeIcon icon={faTimesCircle} /> { isValidateMessage }</span>
                            </div>
                        }
                        <Row className="p-2 pt-0">
                            <div className="col-md-12">
                                <Row>
                                    <div className="col-md-12 p-1">
                                        <Card className="p-1">
                                            <CardBody className="p-1 m-1">
                                                <div className="mt-1 mb-1">
                                                    <TextField
                                                        name="old_password"
                                                        label="Old Password"
                                                        type={isShowOldPass ? "text" : "password"}
                                                        onChange={(e) => setOldPassword(e.target.value)}
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowOldPassword}>
                                                                    {isShowOldPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        type={isShowNewPass ? "text" : "password"}
                                                        name="new_password"
                                                        label="New Password"
                                                        onChange={(e) => handleChangePassword(e.target.value)}
                                                        error={isMinimum}
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowNewPassword}>
                                                                    {isShowNewPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                                <div className="mt-1 mb-1">
                                                    <TextField
                                                        error={isConfirmError}
                                                        type={isShowConfirmPass ? "text" : "password"}
                                                        name="confirm_password"
                                                        label="Confirm Password"
                                                        onChange={(e) => checkConfirmPassword(e.target.value)}
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowConfirmPassword}>
                                                                    {isShowConfirmPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                            </CardBody>
                                        </Card>
                                    </div>
                                </Row>
                            </div>
                            {isProcessing &&
                                <Col md={12}>
                                    <Paper className="p-2 m-2 mt-3">
                                        <LinearProgressBarWithLabel title="Updating Password" />
                                    </Paper>
                                </Col>
                            }
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing || isValidateError} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faSave} /> Save
                                </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Close
                        </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    )


}

export default ChangePasswordModal 