import React, { useState } from 'react'
import API from '../../../utils/Api'
import Swal from 'sweetalert2'
import { useAuth } from "../../../context/auth"

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Col
} from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWindowClose, faSave, faKey, faTimesCircle } from '@fortawesome/free-solid-svg-icons'

import { TextField, InputAdornment, IconButton, Paper } from '@material-ui/core'
import Visibility from '@material-ui/icons/Visibility'
import VisibilityOff from '@material-ui/icons/VisibilityOff'
import LinearProgressBarWithLabel from '../Common/LinearProgressBarWithLabel'

const NewSubAccountsModal = (props) => {
    const authToken = useAuth().authTokens

    const [isProcessing, setIsProcessing] = useState(false)
    const [isValidateError, setIsValidateError] = useState(false)
    const [isValidateMessage, setIsValidateMessage] = useState("Invalid Input")
    const [isMinimum, setIsMinimum] = useState(false)

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [email, setEmail] = useState('')
    const [contact, setContact] = useState('')
    const [address, setAddress] = useState('')

    const [isShowPassword, setIsShowPassword] = useState(false)
    const toggleShowPassword = () => setIsShowPassword(!isShowPassword)


    const newSubAccountHandler = (e) => {
        e.preventDefault()
        const details = authToken.details

        if (isMinimum) {
            setIsValidateMessage("Minimum password length is 6")
        }

        if (!isMinimum) {
            setIsValidateError(false)

            let newSubAccountData = {
                transport_company_id: details.transport_company_id,
                carrier_name: details.carrier_name,
                carrier_contact: contact,
                carrier_address: address,
                carrier_email: email,
                username: username,
                password: password,
                type: 'sub'
            }

            API.post("account/register", newSubAccountData)
                .then((result) => {
                    if (result.status === 200) {
                        if (result.data.error === 0) {
                            Swal.fire(
                                "Transaction Failed",
                                result.data.message,
                                "error"
                            )
                        } else {
                            let res = Swal.fire(
                                "Transaction Completed!",
                                "Account Successfully Registered!",
                                "success"
                            )

                            res.then(() => {
                                props.toggle()
                                props.loadSubAccounts()
                            })
                        }
                    } else if (result.status === 254) {
                        Swal.fire(
                            "Transaction Failed",
                            result.data.message,
                            "error"
                        )
                    } else {
                        Swal.fire(
                            "Transaction Failed",
                            "Registration Failed",
                            "error"
                        )
                    }
                })
                .catch((e) => {
                    Swal.fire(
                        "Transaction Completed!",
                        "Account Successfully Registered!",
                        "success"
                    )
                })
        } else {
            Swal.fire(
                "Transaction Completed!",
                "Account Successfully Registered!",
                "success"
            )
        }
    }

    const handleChangePassword = (value) => {
        setPassword(value)
        setIsValidateError(false)
        if (value.replace(/\s+/g, '').length >= 6) {
            setIsMinimum(false)
        } else {
            setIsMinimum(true)
        }
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-md" style={{ marginLeft: "40%", marginTop: "7%", color: "#1a1751" }}>
                <ModalHeader toggle={props.toggle}>
                    <FontAwesomeIcon icon={faKey} /> New Sub Account <br />
                </ModalHeader>
                <form onSubmit={(e) => newSubAccountHandler(e)}>
                    <ModalBody>
                        {isValidateError &&
                            <div className="text-left card p-1 bg-danger">
                                <span className="text-white ml-2"><FontAwesomeIcon icon={faTimesCircle} /> {isValidateMessage}</span>
                            </div>
                        }
                        <Row className="p-2 pt-0">
                            <div className="col-md-12">
                                <Row>
                                    <div className="col-md-12 p-1">
                                        <Card className="p-1">
                                            <CardBody className="p-1 m-1">
                                                <div className="mt-1 mb-1">
                                                    <TextField
                                                        name="username"
                                                        label="Username"
                                                        onChange={(e) => setUsername(e.target.value)}
                                                        fullWidth
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        type={isShowPassword ? "text" : "password"}
                                                        name="password"
                                                        label="Password"
                                                        onChange={(e) => handleChangePassword(e.target.value)}
                                                        error={isMinimum}
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowPassword}>
                                                                    {isShowPassword ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        name="email"
                                                        placeholder="Email"
                                                        label="Email"
                                                        value={email}
                                                        onChange={(e) => setEmail(e.target.value)}
                                                        fullWidth
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        name="contact"
                                                        placeholder="Contact Number"
                                                        label="Contact Number"
                                                        value={contact}
                                                        onChange={(e) => setContact(e.target.value)}
                                                        fullWidth
                                                        required
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        name="address"
                                                        placeholder="Address"
                                                        label="Address"
                                                        value={address}
                                                        onChange={(e) => setAddress(e.target.value)}
                                                        fullWidth
                                                        required
                                                    />
                                                </div>                                                
                                            </CardBody>
                                        </Card>
                                    </div>
                                </Row>
                            </div>
                            {isProcessing &&
                                <Col md={12}>
                                    <Paper className="p-2 m-2 mt-3">
                                        <LinearProgressBarWithLabel title="Creating new account" />
                                    </Paper>
                                </Col>
                            }
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing || isValidateError} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faSave} /> Save
                        </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Close
                        </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    )


}

export default NewSubAccountsModal