import React, { useState } from "react";
import { Modal, ModalHeader, ModalBody, Row, Col } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserPlus } from "@fortawesome/free-solid-svg-icons";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import PassengerForm from "../Forms/PassengerForm";
import { useTheme } from "../../../context/theme";

const NewPassenger = (props) => {
  const [isProcessing, setIsProcessing] = useState(false);
  const modalMarginXL = useTheme().modalMarginXL;

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-xl"
        style={{ marginTop: "6%", marginLeft: modalMarginXL }}
      >
        <ModalHeader style={{ lineHeight: "2" }} toggle={props.toggle}>
          <FontAwesomeIcon icon={faUserPlus} /> Passenger Profile
        </ModalHeader>
        <ModalBody className="pt-0">
          <Row>            
            <PassengerForm
              setIsProcessing={setIsProcessing}
              toggle={props.toggle}
            />
          </Row>
          <div>
            {isProcessing && (
              <Row className="p-2">
                <Col className="text-center">
                  <LinearProgressBarWithLabel title="Create New Passenger Profile" />
                </Col>
              </Row>
            )}
          </div>
        </ModalBody>
      </Modal>
    </div>
  );
};

export default NewPassenger;
