import React, { useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { useAuth } from "../../../context/auth";
import { useProfile } from "../../../context/profile";
import { TextField } from "@material-ui/core";
import { Container, Row, Col, Button, Card, CardBody, Form } from "reactstrap";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faMapMarkedAlt,
  faPhoneAlt,
  faSave,
  faUser,
  faUsers,
  faPeopleArrows,
  faArrowCircleRight,
  faArrowCircleLeft,
  faInfoCircle,
} from "@fortawesome/free-solid-svg-icons";
import Swal from "sweetalert2";
import API from "../../../utils/Api";
import Error from "../Error/Error";
import GenderSelect from "./Fields/GenderSelect";
import ShipNameSelect from "./Fields/ShipNameSelect";
import AccommodationType from "./Fields/AccommodationType";
import ScheduleSelect from "./Fields/ScheduleSelect";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { Typography } from "@material-ui/core";

const PassengerForm = (props) => {
  const dateFns = new DateFnsUtils();
  const authToken = useAuth().authTokens;
  const profile_context = useProfile();

  const [isProcessing, setIsProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("Invalid Input");
  const [isSelectedShip, setIsSelectedShip] = useState(false);

  const [steps, setSteps] = useState("1");

  const [shipSchedules, setShipSchedules] = useState([]);
  const [scheduleOptions, setScheduleOptions] = useState([]);
  const [isShipWithSchedule, setIsShipWithSchedule] = useState(false);
  const [isSchedulesLoaded, setIsSchedulesLoaded] = useState(true);

  // Personal
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [gender, setGender] = useState("");
  const [age, setAge] = useState("");
  const [bodyTemperature, setBodyTemperature] = useState("");
  // Contact
  const [email, setEmail] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  // Relative
  const [relativeName, setRelativeName] = useState("");
  const [relativeContact, setRelativeContact] = useState("");
  // Address
  const [zipcode, setZipcode] = useState("");
  const [specificAddress, setSpecificAddress] = useState("");
  // Child Companion
  const [childCompanion, setChildCompanion] = useState("");

  // Travel Details
  const [origin, setOrigin] = useState("");
  const [destination, setDestination] = useState("");
  const [travelHistory, setTravelHistory] = useState("");

  // Transport Details
  const [shipName, setShipName] = useState("");
  const [transportType, setTransportType] = useState("");
  const [voyageRoute, setVoyageRoute] = useState("");
  const [departureDate, setDepartureDateChange] = useState(null);
  const [arrivalDate, setArrivalDateChange] = useState(null);
  const [seatNumber, setSeatNumber] = useState("");
  const [parkingNumber, setParkingNumber] = useState("");
  const [transportNumber, setTransportNumber] = useState("");
  const [accommodationType, setAccommodationType] = useState("");
  const [scheduleTransportationId, setScheduleTransportationId] = useState("");

  const goto = (step) => {
    setSteps(step);
  };

  const validate = () => {
    if (!isShipWithSchedule) {
      setIsError(true);
      setErrorMessage("Ship schedule is required");
      return false;
    }

    let d1 = dateFns.date(departureDate);
    let d2 = dateFns.date(arrivalDate);
    let today = dateFns.date(Date.now());

    if (dateFns.isBefore(d1, today)) {
      setIsError(true);
      setErrorMessage("Invalid Departure Date");
      return false;
    }
    
    if (dateFns.isBefore(d2, d1) || dateFns.isEqual(d1, d2)) {
      setIsError(true);
      setErrorMessage("Invalid Arrival Date");
      return false;
    }

    return true;
  };

  const getShipDetails = (shipId) => {
    API.get(`carrier/transport/ship/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    }).then((result) => {
      let data = result.data;
      if (data != null) {
        setVoyageRoute(data.routes);
        setTransportType(data.transportation_category_id);
      }
    });
  };

  const getShipSchedules = (shipId) => {
    setIsSchedulesLoaded(false);
    setScheduleOptions([]);
    API.get(`carrier/transport/voyage/scheduled-ships/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    }).then((result) => {
      if (result.status === 200) {
        let data = result.data;
        setShipSchedules(data);
        if (data.length > 0) {
          let scheduleList = data.map((value, index) => {
            let dates = dateFns.format(
              new Date(value.departure),
              "MMM/d/yyyy h:mm:ss a"
            );

            return {
              value: value.id,
              label: dates,
            };
          });

          if (scheduleList.length > 0) {
            setIsShipWithSchedule(true);
          }

          setScheduleOptions(scheduleList);
          setIsSelectedShip(true);
        }
        setIsSchedulesLoaded(true);
      }
    });
  };

  const handleScheduleSelection = (scheduleid) => {
    let filtered = shipSchedules.filter((d) => {
      return d.id === scheduleid;
    });

    setScheduleTransportationId(scheduleid);
    setOrigin(filtered[0].fromLocation);
    setDestination(filtered[0].toLocation);
    setTransportNumber(filtered[0].voyage);
    setArrivalDateChange(
      dateFns.format(new Date(filtered[0].arrival), "Y-M-dd H:m:s")
    );
    setDepartureDateChange(
      dateFns.format(new Date(filtered[0].departure), "Y-M-dd H:m:s")
    );
  };

  const saveProfileHandler = (e) => {
    e.preventDefault();
    if (steps === "1") {
      goto("2");
      return null;
    }

    if (!validate()) {
      return null;
    }

    props.setIsProcessing(true);
    setIsProcessing(true);

    let postData = {
      first_name: firstName,
      last_name: lastName,
      gender: gender,
      age: age,
      email: email,
      mobile: mobileNumber,
      zipcode: parseInt(zipcode),
      specific_address: specificAddress,
      temp: bodyTemperature,
      barangay: "",
      origin: origin,
      destination: destination,
      arrival: arrivalDate,
      departure: departureDate,
      transport_type: transportType,
      transport_no: parseInt(transportNumber),
      ship_name: shipName,
      accommodation_type: accommodationType,
      previous_travel: travelHistory,
      parking_space_no: parkingNumber,
      seat_no: seatNumber,
      route: voyageRoute,
      companion_name: childCompanion,
      relative_name: relativeName,
      relative_contact: relativeContact,
      scheduled_transportation_id: scheduleTransportationId,
    };

    API.post(`carrier/add-profile`, postData, {
      headers: {
        Authorization: "bearer " + authToken.access_token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          props.setIsProcessing(false);
          setIsProcessing(false);
          let res = Swal.fire(
            "Transaction Completed!",
            "Data Successfully Created!",
            "success"
          );

          res.then(() => {
            profile_context.loadPassengers();
            props.toggle();
          });
        } else {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        props.setIsProcessing(false);
        setIsProcessing(false);

        Swal.fire("Transaction Failed!", "error");
      });
  };

  return (
    <Container fluid className="pt-2">
      {isError && (
        <Col md={12} className="text-left m-0 p-0 mt-1 ml-2">
          <Typography variant="h6">
            <Error className="p-0 m-0">{errorMessage}</Error>
          </Typography>
        </Col>
      )}
      <Col xs={12} className="mt-3">
        <Form onSubmit={(e) => saveProfileHandler(e)}>
          <Row>
            <Col md={12} className="mt-0 pt-0 mb-2 text-danger">
              <h6>
                <FontAwesomeIcon icon={faInfoCircle} /> Note: Please fill in the
                required (*) fields
              </h6>
            </Col>
            {steps === "1" && (
              <Container fluid>
                <Row>
                  <Col xs={4}>
                    <h6 className="text-primary">
                      <FontAwesomeIcon icon={faUser} /> Personal Details
                    </h6>
                    <Row>
                      <Col xs={12}>
                        <Card className="p-1">
                          <CardBody className="pt-1 mt-3">
                            <div>
                              <TextField
                                name="first_name"
                                label="First Name"
                                value={firstName}
                                onChange={(e) => setFirstName(e.target.value)}
                                fullWidth
                                required
                              />
                            </div>
                            <div>
                              <TextField
                                name="last_name"
                                label="Last Name"
                                value={lastName}
                                onChange={(e) => setLastName(e.target.value)}
                                fullWidth
                                required
                              />
                            </div>
                            <div>
                              <TextField
                                type="number"
                                name="age"
                                label="Age"
                                value={age}
                                onChange={(e) => setAge(e.target.value)}
                                fullWidth
                                required
                              />
                            </div>
                            <div>
                              <GenderSelect
                                gender={gender}
                                setGender={setGender}
                              />
                            </div>
                            <div className="mb-4">
                              <TextField
                                type="number"
                                inputProps={{
                                  min: "20.0",
                                  max: "50.0",
                                  step: "0.1",
                                }}
                                name="bodyTemperature"
                                label="Body Temperature"
                                value={bodyTemperature || ""}
                                onChange={(e) =>
                                  setBodyTemperature(e.target.value)
                                }
                                fullWidth
                              />
                            </div>
                          </CardBody>
                        </Card>
                      </Col>
                    </Row>
                  </Col>
                  <Col xs={4} className="p-0">
                    <h6 className="text-primary">
                      <FontAwesomeIcon icon={faPhoneAlt} /> Contact Details
                    </h6>
                    <Row>
                      <Col xs={12}>
                        <Card className="p-1">
                          <CardBody className="pt-1">
                            <div>
                              <TextField
                                name="mobile"
                                type="number"
                                label="Mobile Number"
                                value={mobileNumber || ""}
                                onChange={(e) =>
                                  setMobileNumber(e.target.value)
                                }
                                fullWidth
                                required
                              />
                            </div>
                            <div>
                              <TextField
                                type="email"
                                name="email"
                                label="Email Address"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                fullWidth
                              />
                            </div>
                          </CardBody>
                        </Card>
                      </Col>
                    </Row>
                    <h6 className="text-primary mt-2 ml-1">
                      <FontAwesomeIcon icon={faMapMarkedAlt} /> Address
                    </h6>
                    <Row>
                      <Col xs={12}>
                        <Card className="p-1">
                          <CardBody className="pt-1">
                            <div className="mt-2">
                              <TextField
                                type="number"
                                name="zipcode"
                                label="Zipcode"
                                value={zipcode}
                                onChange={(e) => setZipcode(e.target.value)}
                                fullWidth
                                required
                              />
                            </div>
                            <div className="mb-2">
                              <TextField
                                label="Complete Address"
                                name="complete_address"
                                value={specificAddress}
                                onChange={(e) =>
                                  setSpecificAddress(e.target.value)
                                }
                                fullWidth
                                required
                              />
                            </div>
                          </CardBody>
                        </Card>
                      </Col>
                    </Row>
                  </Col>
                  <Col xs={4}>
                    <h6 className="text-primary">
                      <FontAwesomeIcon icon={faUsers} /> Companion Details
                    </h6>
                    <Row>
                      <Col xs={12}>
                        <Card className="p-1">
                          <CardBody className="pt-1">
                            <div className="mt-2 mb-2">
                              <TextField
                                label="Child Companion"
                                name="childCompanion"
                                value={childCompanion}
                                helperText="Use comma (,) as separator for multiple child"
                                onChange={(e) =>
                                  setChildCompanion(e.target.value)
                                }
                                fullWidth
                              />
                            </div>
                          </CardBody>
                        </Card>
                      </Col>
                    </Row>
                    <h6 className="text-primary mt-3">
                      <FontAwesomeIcon icon={faPeopleArrows} /> Nearest Relative
                    </h6>
                    <Row>
                      <Col xs={12}>
                        <Card className="p-1">
                          <CardBody className="pt-1">
                            <div className="mt-2">
                              <TextField
                                label="Name"
                                name="relativeName"
                                value={relativeName}
                                onChange={(e) =>
                                  setRelativeName(e.target.value)
                                }
                                fullWidth
                                required
                              />
                            </div>
                            <div className="mb-2">
                              <TextField
                                type="number"
                                label="Contact Number"
                                name="relativeContact"
                                value={relativeContact}
                                onChange={(e) =>
                                  setRelativeContact(e.target.value)
                                }
                                fullWidth
                                required
                              />
                            </div>
                          </CardBody>
                        </Card>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Container>
            )}
            {steps === "2" && (
              <Container fluid>
                <Row>
                  <Col md={12}>
                    <h6 className="text-primary">
                      <DirectionsBoatIcon /> Transport Details
                    </h6>
                  </Col>
                  <Col md={4}>
                    <ShipNameSelect
                      shipName={shipName}
                      setShipName={setShipName}
                      getShipDetails={getShipDetails}
                      getShipSchedules={getShipSchedules}
                    />
                    <TextField
                      className="m-0 p-0 mt-1"
                      value={seatNumber}
                      onChange={(e) => setSeatNumber(e.target.value)}
                      name="seat_number"
                      label="Seat/Bunk Number"
                      fullWidth
                      required
                    />
                    <TextField
                      hidden
                      className="m-0 p-0 mt-1"
                      onChange={(e) => setTransportNumber(e.target.value)}
                      value={transportNumber}
                      name="transportNumber"
                      label="Voyage Number"
                      fullWidth
                      required
                    />
                  </Col>
                  <Col md={4}>
                    <TextField
                      hidden
                      className="mb-1"
                      name="voyageRoute"
                      label="Voyage Route"
                      value={voyageRoute}
                      onChange={(e) =>
                        setVoyageRoute(e.target.value.toUpperCase())
                      }
                      fullWidth
                      disabled={true}
                    />
                    {isSchedulesLoaded && (
                      <ScheduleSelect
                        handleScheduleSelection={(id) =>
                          handleScheduleSelection(id)
                        }
                        scheduleOptions={scheduleOptions}
                        disabled={isSelectedShip}
                      />
                    )}
                    {!isSchedulesLoaded && (
                      <div className="mb-1">
                        <LinearProgressBarWithLabel title="Loading Schedules..." />
                      </div>
                    )}
                    <TextField
                      name="parking_space_no"
                      label="Parking Space Number"
                      value={parkingNumber}
                      helperText="( if with service vehicle )"
                      onChange={(e) => setParkingNumber(e.target.value)}
                      fullWidth
                    />
                  </Col>
                  <Col md={4}>
                    <AccommodationType
                      setAccommodationType={setAccommodationType}
                      accommodationType={accommodationType}
                    />
                    <TextField
                      className="mt-1"
                      label="Place of Travel History"
                      name="travelHistory"
                      helperText="( within 14 Days )"
                      value={travelHistory || ""}
                      onChange={(e) => setTravelHistory(e.target.value)}
                      fullWidth
                      required
                    />
                  </Col>
                </Row>
              </Container>
            )}
          </Row>
          {steps === "1" && (
            <Row className="mt-3 mr-1 text-right d-flex">
              <Button
                className="ml-auto"
                type="submit"
                color="primary"
                style={{ minWidth: "120px" }}
              >
                Next{" "}
                <FontAwesomeIcon
                  icon={faArrowCircleRight}
                  className="mt-1 ml-1"
                />
              </Button>
            </Row>
          )}
          {steps === "2" && (
            <div>
              {!isProcessing && (
                <Row className="mt-3 mr-1 text-right d-flex">
                  <Button
                    className="ml-auto mr-1"
                    type="button"
                    onClick={() => goto("1")}
                    color="primary"
                    style={{ minWidth: "120px" }}
                  >
                    <FontAwesomeIcon
                      icon={faArrowCircleLeft}
                      className="mt-1 ml-1"
                    />{" "}
                    Back
                  </Button>
                  <Button
                    type="submit"
                    color="primary"
                    style={{ minWidth: "120px" }}
                  >
                    Save <FontAwesomeIcon icon={faSave} className="mt-1 ml-1" />
                  </Button>
                </Row>
              )}
            </div>
          )}
        </Form>
      </Col>
    </Container>
  );
};

export default PassengerForm;
