import React, { useState } from "react";
import API from "../../../utils/Api";
import Swal from "sweetalert2";
import DateFnsUtils from "@date-io/date-fns";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Card,
  CardBody,
  Row,
  Input,
  Col,
} from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faWindowClose,
  faAnchor,
  faFileExcel,
  faFileDownload,
  faInfoCircle,
  faCheck,
} from "@fortawesome/free-solid-svg-icons";
import FileTemplate from "../../../assets/template/Template.xlsx";
import { useAuth } from "../../../context/auth";
import { useProfile } from "../../../context/profile";
import ShipNameSelect from "../Forms/Fields/ShipNameSelect";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import ScheduleSelect from "../Forms/Fields/ScheduleSelect";

const NewPassengerManifests = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const profile_context = useProfile();
  const dateFns = new DateFnsUtils();

  const [isProcessing, setIsProcessing] = useState(false);

  const [excelFile, setExcelFile] = useState(null);
  const [departure, setDeparture] = useState(null);
  const [arrival, setArrival] = useState(null);
  const [transportNumber, setTransportNumber] = useState("");
  const [shipName, setShipName] = useState("");
  const [transportType, setTransportType] = useState(0);
  const [voyageRoute, setVoyageRoute] = useState("");

  const [shipSchedules, setShipSchedules] = useState([]);
  const [scheduleOptions, setScheduleOptions] = useState([
    { value: "", label: "" },
  ]);
  const [isShipWithSchedule, setIsShipWithSchedule] = useState(false);
  const [isSchedulesLoaded, setIsSchedulesLoaded] = useState(true);
  const [scheduleTransportationId, setScheduleTransportationId] = useState("");

  const getShipDetails = (shipId) => {
    API.get(`carrier/transport/ship/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    }).then((result) => {
      let data = result.data;
      if (data != null) {
        setVoyageRoute(data.routes);
        setTransportType(data.transportation_category_id);
      }
    });
  };

  const getShipSchedules = (shipId) => {
    setIsSchedulesLoaded(false);
    setIsShipWithSchedule(false);
    setScheduleOptions([]);
    API.get(`carrier/transport/voyage/scheduled-ships/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          let data = result.data;
          setShipSchedules(data);
          if (data.length > 0) {
            let scheduleList = data.map((value, index) => {
              let dates = dateFns.format(
                new Date(value.departure),
                "MMM/d/yyyy h:mm:ss a"
              );

              return {
                value: value.id,
                label: dates,
              };
            });

            setScheduleOptions(scheduleList);
            if (props.setIsPreparingData) {
              props.setIsPreparingData(false);
            }
          } else {
            setScheduleTransportationId("");
            setScheduleOptions([{ value: "", label: "" }]);
          }
        }
        setIsSchedulesLoaded(true);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const handleScheduleSelection = (scheduleid) => {
    let filtered = shipSchedules.filter((d) => {
      return d.id === scheduleid;
    });

    if (filtered.length > 0) {
      setIsShipWithSchedule(true);
      setScheduleTransportationId(scheduleid);
      setTransportNumber(filtered[0].voyage);
      setArrival(dateFns.format(new Date(filtered[0].arrival), "Y-M-dd H:m:s"));
      setDeparture(
        dateFns.format(new Date(filtered[0].departure), "Y-M-dd H:m:s")
      );
    }
  };

  const importFileHandler = (e) => {
    e.preventDefault();

    setIsProcessing(true);
    let token = authToken;
    let formData = new FormData();

    formData.append("profileFile", excelFile);
    formData.append(
      "departure",
      dateFns.format(new Date(departure), "Y-M-dd H:m:s")
    );
    formData.append(
      "arrival",
      dateFns.format(new Date(arrival), "Y-M-dd H:m:s")
    );
    formData.append("transport_no", parseInt(transportNumber));
    formData.append("ship_name", shipName);
    formData.append("transport_type", transportType);
    formData.append("route", voyageRoute);
    formData.append("scheduled_transportation_id", scheduleTransportationId);

    API.post("store/import/profiles", formData, {
      headers: {
        Authorization: "bearer " + token,
        "Content-Type": "multipart/form-data",
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false);
          setExcelFile(null);
          setDeparture(null);
          setArrival(null);
          setTransportNumber("");
          setTransportType("");
          setVoyageRoute("");
          setScheduleTransportationId("");

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Imported!",
            "success"
          );

          profile_context.loadPassengers();
          props.toggle();
        } else {
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        setIsProcessing(false);
        Swal.fire("Transaction Failed!", "Failed to Import Data!", "error");
      });
  };

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-lg"
        style={{ marginTop: "7%", marginLeft: "30%" }}
      >
        <ModalHeader style={{ lineHeight: "2" }} toggle={props.toggle}>
          <FontAwesomeIcon icon={faFileExcel} /> Import Passenger Manifest{" "}
        </ModalHeader>
        <form
          onSubmit={(e) => importFileHandler(e)}
          encType="multipart/form-data"
          id="excelFile"
        >
          <ModalBody>
            <Row className="p-2">
              <Col md={12} className="mt-0 pt-0 mb-2 text-danger">
                <h6>
                  <FontAwesomeIcon icon={faInfoCircle} /> Note: Please fill in
                  the required (*) fields and use only the required template
                </h6>
              </Col>
              <Col md={6} className="p-2 pl-4">
                <h6 className="text-primary ml-1">
                  <FontAwesomeIcon icon={faAnchor} /> Transport Details
                </h6>
                <div>
                  <ShipNameSelect
                    setShipName={setShipName}
                    getShipDetails={getShipDetails}
                    getShipSchedules={getShipSchedules}
                  />
                </div>
              </Col>
              <Col md={6} className="p-2 pr-4 mt-4">
                <div className="p-0 m-0 mt-1">
                  {isSchedulesLoaded && (
                    <ScheduleSelect
                      handleScheduleSelection={(id) =>
                        handleScheduleSelection(id)
                      }
                      scheduleOptions={scheduleOptions}
                      selectedSchedule={scheduleTransportationId}
                      disabled={!isShipWithSchedule}
                    />
                  )}
                  {!isSchedulesLoaded && (
                    <div className="mb-1">
                      <LinearProgressBarWithLabel title="Loading Schedules..." />
                    </div>
                  )}
                </div>
              </Col>
              <Col md={12} className="pt-2 mt-2">
                <h6>
                  <a
                    href={FileTemplate}
                    title="Download Passenger Manifest Template"
                    download="Passenger-Manifest-Template.xlsx"
                  >
                    <span style={{ fontSize: "1rem" }}>
                      <FontAwesomeIcon icon={faFileDownload} /> Download this
                      template
                    </span>
                  </a>
                  <br />
                </h6>
                <Card>
                  <CardBody className="p-1 m-0">
                    <div className="m-0">
                      <Input
                        accept=".xlsx,.xls"
                        id="raised-button-file"
                        type="file"
                        onChange={(e) => setExcelFile(e.target.files[0])}
                        disabled={isProcessing}
                        required
                      />
                    </div>
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Importing Passenger Manifest" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter style={{ justifyContent: "flex-end" }}>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faCheck} /> Import
            </Button>{" "}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default NewPassengerManifests;
