import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import LinearProgress from '@material-ui/core/LinearProgress'
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'

function LinearProgressBar(props) {
    return (
        <Box display="flex" alignItems="center">
            <Box width="100%" mr={1}>
                <LinearProgress variant="determinate" {...props} />
            </Box>
            <Box minWidth={35}>
                <Typography variant="body2" color="textSecondary">{`${Math.round(
                    props.value,
                )}%`}</Typography>
            </Box>
        </Box>
    )
}

const useStyles = makeStyles({
    root: {
        width: '100%',
    },
})

LinearProgressBar.propTypes = {
    /**
     * The value of the progress indicator for the determinate and buffer variants.
     * Value between 0 and 100.
     */
    value: PropTypes.number.isRequired,
}


const LinearProgressBarWithLabel = (props) => {
    const classes = useStyles()
    const [progress, setProgress] = useState(0)


    useEffect(() => {
        const timer = setInterval(() => {

            setProgress((prevProgress) => ((prevProgress < 89 && prevProgress < 100) ?  prevProgress + Math.floor(Math.random() * 10) + 1 : 99))


        }, 800)
        return () => {
            clearInterval(timer)
        }
    }, [])

    return (
        <div className={classes.root}>
            <Typography>{props.title}</Typography>
            <LinearProgressBar value={progress} />
        </div>
    )
}

export default LinearProgressBarWithLabel
