import Axios from "axios"
import API from "./Api"

export const loadShips = (token) => {
  let cancelToken = Axios.cancelToken
  return API.get("carrier/transport/ships", {
    cancelToken: cancelToken,
    headers: {
      Authorization: "bearer " + token.access_token,
    },
  })
}

export const loadTransportTypes = (token) => {
  let cancelToken = Axios.cancelToken
  return API.get("carrier/transport/category", {
    cancelToken: cancelToken,
    headers: {
      Authorization: "bearer " + token.access_token,
    },
  })
}

export const loadCompanies = () => {
  let cancelToken = Axios.cancelToken
  return API.get("company", {
    cancelToken: cancelToken,
  })
}
