import React, { useState, useEffect, useCallback } from "react";
import Swal from "sweetalert2";

import { useAuth } from "../../../context/auth";
import { useOptions } from "../../../context/options";
import { ShipContext } from "../../../context/ship";
import API from "../../../utils/Api";
import { debounce, isArray } from "lodash";

import { Tooltip } from "@material-ui/core";
import { Container, Button, Row, Col } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArchive, faEdit } from "@fortawesome/free-solid-svg-icons";
import DataTable from "../../UI/Datatable/Datatable";
import MenuButton from "../../UI/Common/OptionMenuShipsButton";
import NewShip from "../../UI/Modals/NewShip";
import UpdateShip from "../../UI/Modals/UpdateShip";

import "./ships.css";
import Axios from "axios";

const Ships = (props) => {
  const token = useAuth().authTokens;
  const options_context = useOptions();
  const [shipsData, setShipData] = useState([]);
  const [selectedShip, setSelectedShip] = useState({});
  const [currentPage, setCurrentPage] = useState();
  const [isLoading, setIsLoading] = useState(false);

  // Modals
  const [newShip, setNewShip] = useState(false);
  const toggleNewShip = () => setNewShip(!newShip);
  const [updateShip, setUpdateShip] = useState(false);
  const toggleUpdateShip = () => setUpdateShip(!updateShip);

  const formatData = (data = null) => {
    if (data === null) {
      return null;
    }

    if (isArray(data)) {
      return formatArrayData(data);
    }

    return formatObjectData(data);
  };

  const formatObjectData = (data) => {
    let ship = [];
    let index = 0;

    for (const [key, value] of Object.entries(data)) {
      ship[index] = [
        value.id,
        value.name,
        value.routes,
        value.capacity,
        [value, index, data],
      ];

      index++;
    }

    return ship;
  };

  const formatArrayData = (data) => {
    if (data.length > 0) {
      return data.map((value, index) => {
        let ship;

        ship = [
          value.id,
          value.name,
          value.routes,
          value.capacity,
          [value, index, data],
        ];

        return ship;
      });
    }
  };

  const loadShips = useCallback(() => {
    setIsLoading(true);
    let cancelToken = Axios.cancelToken;
    API.get("carrier/transport/ships", {
      cancelToken: cancelToken,
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    })
      .then((result) => {
        let data = result.data;
        let currentPage = result.data.current_page;
        if (Object.keys(data).length > 0) {
          let ships = formatData(data);
          setCurrentPage(currentPage);
          setShipData(ships);
        }

        setIsLoading(false);
      })
      .catch((e) => {
        setIsLoading(false);
      });
  }, []); 

  const appendShip = (page) => {
    API.get(`carrier/transport?page=${page}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let data = result.data.data;

      let newShipData = [];
      let ships = formatData(data);

      if (result.data.current_page > currentPage) {
        setCurrentPage(result.data.current_page);
        if (ships !== undefined) {
          newShipData = [...shipsData, ...ships];
        } else {
          newShipData = shipsData;
        }
      }

      setShipData(newShipData);
    });
  };

  const debounceSearchHandler = debounce(
    (queryData) => searchShips(queryData),
    500
  );

  const searchShips = (query) => {
    return API.post(
      `carrier/transport/search`,
      {
        name: query,
      },
      {
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    ).then((result) => {
      let data = result.data;
      let ships = formatData(data);
      setShipData(ships);
      setIsLoading(false);
    });
  };

  const archiveHandler = (data) => {
    let shipData = data[2];
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        API.delete(`carrier/transport/delete/${data[0].id}`, {
          headers: {
            Authorization: "bearer " + token.access_token,
          },
        }).then((result) => {
          if (result.data.status) {
            Swal.fire("Transaction Completed", result.data.message, "success");
            shipData.splice(data[1], 1);
            shipData = formatData(shipData);
            setShipData(shipData);
            options_context.getShipsOptions();
          } else {
            Swal.fire("Transaction Completed", result.data.message, "error");
          }
        });
      }
    });
  };

  const handleUpdateShip = (value) => {
    setSelectedShip(value);
    toggleUpdateShip();
  };

  const columns = [
    "ID",
    "Name",
    "Route",
    "Capacity",
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div style={{ color: "#1a1751" }}>
              <Tooltip title="Update Ship Details" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn"
                  onClick={() => handleUpdateShip(value)}
                >
                  <FontAwesomeIcon icon={faEdit} />
                </Button>
              </Tooltip>
              <Tooltip title="Archive Ship" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn text-danger"
                  onClick={() => archiveHandler(value)}
                >
                  <FontAwesomeIcon icon={faArchive} />
                </Button>
              </Tooltip>
            </div>
          );
        },
      },
    },
  ];

  const options = {
    fixHeader: true,
    filterType: "dropdown",
    responsive: "vertical",
    tableBodyHeight: "350px",
    selectableRows: "none",
    filter: false,
    viewColumns: false,
    elevation: 0,
    print: false,
    download: false,
    rowsPerPage: 5,
    rowsPerPageOptions: [5, 30, 100],
    onChangePage: (p) => {
      if (p % 2 !== 0) {
        if (p >= currentPage) {
          appendShip(currentPage + 1);
        }
      }
    },
    onSearchClose: () => {
      setIsLoading(false);
      loadShips();
    },
    searchProps: {
      onKeyUp: (e) => {
        let queryString = e.target.value;
        if (queryString.length >= 2) {
          setIsLoading(true);
          debounceSearchHandler(queryString);
        }
      },
    },
    // this is to disable auto search on local data
    customSearch: (query, row, col) => {
      return shipsData.length > 0;
    },
  };

  useEffect(() => {
    loadShips();
    return () => {};
  }, [loadShips]);

  return (
    <ShipContext.Provider value={{ shipsData, selectedShip, loadShips }}>
      <Container fluid="true" className="p-0 m-0">
        <Row className="m-0 p-0 text-right">
          <Col md={12}>
            <Row>
              <Col md={12} className="m-0 p-0">
                <MenuButton toggle={toggleNewShip} />
              </Col>
            </Row>
          </Col>
          <Col md={12} className="pl-0 ml-0 pr-0 mr-0 text-left">
            <DataTable
              title="Ships"
              data={shipsData}
              isLoading={isLoading}
              columns={columns}
              options={options}
            />
          </Col>
        </Row>
      </Container>
      {newShip && <NewShip isOpen={newShip} toggle={toggleNewShip} />}
      {updateShip && (
        <UpdateShip isOpen={updateShip} toggle={toggleUpdateShip} />
      )}
    </ShipContext.Provider>
  );
};

export default Ships;
