import React, { useState, useEffect } from "react";
import { useAuth } from "../../../context/auth";
import { useProfile } from "../../../context/profile";
import DateFnsUtils from "@date-io/date-fns";
import Swal from "sweetalert2";
import API from "../../../utils/Api";
import { FormControlLabel, Switch, TextField } from "@material-ui/core";
import { Container, Row, Col, Button, Card, CardBody, Form } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave } from "@fortawesome/free-solid-svg-icons";
import AccommodationType from "../Forms/Fields/AccommodationType";
import ShipNameSelect from "./Fields/ShipNameSelect";
import ScheduleSelect from "./Fields/ScheduleSelect";
import DirectionsBoatIcon from "@material-ui/icons/DirectionsBoat";
import Error from "../Error/Error";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { Typography } from "@material-ui/core";

const TransportDetailsForm = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const selectedProfile = useProfile().selectedProfile;
  const profile_context = useProfile();
  const dateFns = new DateFnsUtils();

  const [isProcessing, setIsProcessing] = useState(false);
  const [isError, setIsError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("Invalid Input");
  const [isEdit, setIsEdit] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true);
  const [theProfile, setTheProfile] = useState("");

  // Travel Details
  const [origin, setOrigin] = useState("");
  const [destination, setDestination] = useState("");
  const [travelHistory, setTravelHistory] = useState("");

  // Transport Details
  const [shipName, setShipName] = useState("");
  const [transportType, setTransportType] = useState(0);
  const [voyageRoute, setVoyageRoute] = useState("");
  const [departureDate, setDepartureDateChange] = useState(null);
  const [arrivalDate, setArrivalDateChange] = useState(null);
  const [seatNumber, setSeatNumber] = useState("");
  const [parkingNumber, setParkingNumber] = useState("");
  const [transportNumber, setTransportNumber] = useState("");
  const [accommodationType, setAccommodationType] = useState("");

  const [shipSchedules, setShipSchedules] = useState([]);
  const [scheduleOptions, setScheduleOptions] = useState([]);
  const [isShipWithSchedule, setIsShipWithSchedule] = useState(true);
  const [isSchedulesLoaded, setIsSchedulesLoaded] = useState(true);
  const [scheduleTransportationId, setScheduleTransportationId] = useState(
    selectedProfile[1].scheduled_transportation_id
  );

  useEffect(() => {
    let transport_details;
    if (typeof selectedProfile[1].vehicle_details === "object") {
      transport_details = selectedProfile[1].vehicle_details;
    } else {
      transport_details = JSON.parse(selectedProfile[1].vehicle_details);
    }

    setTheProfile(selectedProfile);
    // Travel
    setOrigin(selectedProfile[1].fromLocation);
    setDestination(selectedProfile[1].toLocation);
    setDepartureDateChange(
      dateFns.format(
        new Date(selectedProfile[1].fromDate),
        "MMM/d/yyyy h:mm:ss a"
      )
    );
    setArrivalDateChange(
      dateFns.format(
        new Date(selectedProfile[1].toDate),
        "MMM/d/yyyy h:mm:ss a"
      )
    );
    setTravelHistory(selectedProfile[1].previous_place_travel);

    // // Transport
    setShipName(transport_details.transport_vehicle);
    setTransportType(selectedProfile[1].mode_of_travel_id);
    setTransportNumber(parseInt(transport_details.transport_no));
    setSeatNumber(selectedProfile[1].seat_no);
    setParkingNumber(selectedProfile[1].parking_space_no);
    setAccommodationType(selectedProfile[1].accommodation_type);
    setVoyageRoute(selectedProfile[1].route.toUpperCase());
    getShipSchedules(transport_details.transport_vehicle);
    return () => {};
  }, []);

  const validate = () => {
    if (!isShipWithSchedule) {
      setIsError(true);
      setErrorMessage("Ship schedule is required");
      return false;
    }

    let d1 = dateFns.date(departureDate);
    let d2 = dateFns.date(arrivalDate);
    let today = dateFns.date(Date.now());

    if (dateFns.isBefore(d1, today)) {
      setIsError(true);
      setErrorMessage("Invalid Departure Date");
      return false;
    }

    if (dateFns.isBefore(d2, d1) || dateFns.isEqual(d1, d2)) {
      setIsError(true);
      setErrorMessage("Invalid Arrival Date");
      return false;
    }

    return true;
  };

  const handleEdit = () => {
    setIsEdit(!isEdit);
    if (isDisabled) {
      setIsDisabled(null);
    } else {
      setIsDisabled(true);
    }
  };

  const checkJSON = (str) => {
    try {
      JSON.parse(str);
    } catch (e) {
      return false;
    }
    return true;
  };

  const updateProfileHandler = (e) => {
    e.preventDefault();

    if (!validate()) {
      return null;
    }

    props.setIsProcessing(true);
    setIsProcessing(true);
    let token = authToken;
    let childName = "";

    let profile = theProfile[1];

    // Companion
    if (selectedProfile[1].companions.length > 0) {
      if (checkJSON(selectedProfile[1].companions[0].name)) {
        if (selectedProfile[1].companions[0].name !== null) {
          let companions = JSON.parse(selectedProfile[1].companions[0].name);
          companions = JSON.parse(companions[0].name);
          childName = companions[0].name;
        } else {
          childName = "";
        }
      } else {
        childName = selectedProfile[1].companions[0].name;
      }
    }

    let postData = {
      first_name: profile.profile.first_name,
      last_name: profile.profile.last_name,
      gender: profile.profile.gender,
      age: profile.profile.age,
      email: profile.profile.email,
      mobile: profile.profile.mobile,
      zipcode: profile.profile.address.zipcode,
      specific_address: profile.profile.address.specific_address,
      temperature: profile.temperature,
      barangay: profile.profile.address.barangay,
      person_type: profile.profile.person_type,
      origin: origin,
      destination: destination,
      arrival: dateFns.format(new Date(arrivalDate), "Y-M-dd H:m:s"),
      departure: dateFns.format(new Date(departureDate), "Y-M-dd H:m:s"),
      transport_type: transportType,
      transport_no: parseInt(transportNumber),
      ship_name: shipName,
      travel_id: profile.id,
      accommodation_type: accommodationType,
      previous_travel: travelHistory,
      parking_space_no: parkingNumber,
      seat_no: seatNumber,
      route: voyageRoute,
      companion_name: childName,
      relative_name: profile.profile.relatives[0].name,
      relative_contact: profile.profile.relatives[0].contact,
      scheduled_transportation_id: scheduleTransportationId,
    };

    API.put(`carrier/update/${profile.profile.id}`, postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Updated!",
            "success"
          );

          profile_context.loadPassengers();
          props.toggle();
        } else {
          props.setIsProcessing(false);
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        props.setIsProcessing(false);
        setIsProcessing(false);

        Swal.fire("Transaction Failed!", "error");
      });
  };

  const getShipDetails = (shipId) => {
    API.get(`carrier/transport/ship/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    }).then((result) => {
      let data = result.data;
      if (data != null) {
        setVoyageRoute(data.routes);
        setTransportType(data.transportation_category_id);
      }
    });
  };

  const getShipSchedules = (shipId) => {
    setIsSchedulesLoaded(false);
    setScheduleOptions([]);
    API.get(`carrier/transport/voyage/scheduled-ships/${shipId}`, {
      headers: {
        Authorization: "bearer " + authToken,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          let data = result.data;
          setShipSchedules(data);
          if (data.length > 0) {
            let scheduleList = data.map((value, index) => {
              let dates = dateFns.format(
                new Date(value.departure),
                "MMM/d/yyyy h:mm:ss a"
              );

              return {
                value: value.id,
                label: dates,
              };
            });

            setScheduleOptions(scheduleList);
            if (props.setIsPreparingData) {
              props.setIsPreparingData(false);
            }
          } else {
            setScheduleTransportationId("");
            setScheduleOptions([{ value: "", label: "" }]);
          }
        }
        setIsSchedulesLoaded(true);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const handleScheduleSelection = (scheduleid) => {
    let filtered = shipSchedules.filter((d) => {
      return d.id === scheduleid;
    });

    if (filtered.length > 0) {
      setIsShipWithSchedule(true);
      setScheduleTransportationId(scheduleid);
      setOrigin(filtered[0].fromLocation);
      setDestination(filtered[0].toLocation);
      setTransportNumber(filtered[0].voyage);
      setArrivalDateChange(
        dateFns.format(new Date(filtered[0].arrival), "Y-M-dd H:m:s")
      );
      setDepartureDateChange(
        dateFns.format(new Date(filtered[0].departure), "Y-M-dd H:m:s")
      );
    }
  };

  return (
    <Container fluid className="pt-2">
      {!props.isPreparingData && (
        <div>
          {isError && (
            <Col md={12} className="text-left m-0 p-0 ml-3">
              <Typography variant="h6">
                <Error className="p-0 m-0">{errorMessage}</Error>
              </Typography>
            </Col>
          )}
          <FormControlLabel
            className="ml-2"
            control={
              <Switch checked={isEdit} onChange={handleEdit} color="primary" />
            }
            label="Edit"
          />
          <Form onSubmit={(e) => updateProfileHandler(e)}>
            <Col md={12} className="mt-3">
              <Row>
                <Col md={12} className="text-left mt-0">
                  <Card className="p-0" style={{ border: "none" }}>
                    <CardBody className="m-0 p-0">
                      <Col md={12} className="m-0 p-0">
                        <Container fluid>
                          <Row>
                            <Col md={12}>
                              <h6 className="text-primary">
                                <DirectionsBoatIcon /> Transport Details
                              </h6>
                            </Col>
                            <Col md={4}>
                              <ShipNameSelect
                                shipName={shipName}
                                setShipName={setShipName}
                                getShipDetails={getShipDetails}
                                getShipSchedules={getShipSchedules}
                                disabled={isDisabled}
                              />
                              <TextField
                                className="m-0 p-0 mt-1"
                                value={seatNumber}
                                onChange={(e) => setSeatNumber(e.target.value)}
                                name="seat_number"
                                label="Seat/Bunk Number"
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                              <TextField
                                hidden
                                className="m-0 p-0 mt-1"
                                onChange={(e) =>
                                  setTransportNumber(e.target.value)
                                }
                                value={transportNumber}
                                name="transportNumber"
                                label="Voyage Number"
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </Col>
                            <Col md={4}>
                              <TextField
                                hidden
                                className="mb-1"
                                name="voyageRoute"
                                label="Voyage Route"
                                value={voyageRoute}
                                onChange={(e) =>
                                  setVoyageRoute(e.target.value.toUpperCase())
                                }
                                fullWidth
                                disabled={isDisabled}
                              />
                              <TextField
                                hidden
                                className="mb-1"
                                name="voyageRoute"
                                label="Voyage Route"
                                value={voyageRoute}
                                onChange={(e) =>
                                  setVoyageRoute(e.target.value.toUpperCase())
                                }
                                fullWidth
                                disabled={isDisabled}
                              />
                              {isSchedulesLoaded && (
                                <ScheduleSelect
                                  handleScheduleSelection={(id) =>
                                    handleScheduleSelection(id)
                                  }
                                  scheduleOptions={scheduleOptions}
                                  disabled={!isDisabled}
                                  selectedSchedule={scheduleTransportationId}
                                />
                              )}

                              {!isSchedulesLoaded && (
                                <div className="mb-1">
                                  <LinearProgressBarWithLabel title="Loading Schedules..." />
                                </div>
                              )}

                              <TextField
                                name="parking_space_no"
                                label="Parking Space Number"
                                value={parkingNumber}
                                helperText="( if with service vehicle )"
                                onChange={(e) =>
                                  setParkingNumber(e.target.value)
                                }
                                fullWidth
                                disabled={isDisabled}
                              />
                            </Col>
                            <Col md={4}>
                              <AccommodationType
                                setAccommodationType={setAccommodationType}
                                accommodationType={accommodationType}
                                disabled={isDisabled}
                              />
                              <TextField
                                className="mt-1"
                                label="Place of Travel History"
                                name="travelHistory"
                                helperText="( within 14 Days )"
                                value={travelHistory || ""}
                                onChange={(e) =>
                                  setTravelHistory(e.target.value)
                                }
                                fullWidth
                                required
                                disabled={isDisabled}
                              />
                            </Col>
                          </Row>
                        </Container>
                      </Col>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              {!isProcessing && (
                <div>
                  {isEdit && (
                    <Row className="mt-3 ml-1">
                      <Button
                        type="submit"
                        color="primary"
                        disabled={isDisabled}
                        style={{ minWidth: "120px" }}
                      >
                        <FontAwesomeIcon icon={faSave} /> Save
                      </Button>
                    </Row>
                  )}
                </div>
              )}
            </Col>
          </Form>
        </div>
      )}
    </Container>
  );
};

export default TransportDetailsForm;
